// Supervision Service for JavaScript ReactBits IDE
class SupervisionService {
  constructor() {
    this.state = {
      isEnabled: false,
      autonomyLevel: 'balanced',
      supervisors: [],
      decisions: [],
      qualityGates: {
        security: { threshold: 8, current: 0 },
        performance: { threshold: 7, current: 0 },
        codeQuality: { threshold: 8, current: 0 }
      },
      sessionId: this.generateSessionId()
    };
    
    this.listeners = [];
    this.decisionQueue = [];
    this.isProcessingDecision = false;
  }

  generateSessionId() {
    return 'supervision_' + Date.now() + '_' + Math.random().toString(36).substr(2, 9);
  }

  // State management
  getState() {
    return { ...this.state };
  }

  setState(newState) {
    this.state = { ...this.state, ...newState };
    this.notifyListeners();
  }

  subscribe(listener) {
    this.listeners.push(listener);
    return () => {
      this.listeners = this.listeners.filter(l => l !== listener);
    };
  }

  notifyListeners() {
    this.listeners.forEach(listener => listener(this.state));
  }

  // Supervision control
  enableSupervision(config = {}) {
    this.setState({
      isEnabled: true,
      autonomyLevel: config.autonomyLevel || 'balanced',
      sessionId: this.generateSessionId()
    });
    
    this.logDecision({
      type: 'SYSTEM',
      description: 'Supervision enabled',
      result: 'APPROVED',
      timestamp: Date.now()
    });
    
    return true;
  }

  disableSupervision() {
    this.setState({
      isEnabled: false,
      supervisors: [],
      decisions: [],
      qualityGates: {
        security: { threshold: 8, current: 0 },
        performance: { threshold: 7, current: 0 },
        codeQuality: { threshold: 8, current: 0 }
      }
    });
    
    return true;
  }

  // Supervisor management
  addSupervisor(personaId) {
    const personas = {
      frontend: { id: 'frontend', name: 'Frontend Expert', description: 'UI/UX and React expertise' },
      backend: { id: 'backend', name: 'Backend Expert', description: 'API and server architecture' },
      security: { id: 'security', name: 'Security Expert', description: 'Security auditing and best practices' },
      performance: { id: 'performance', name: 'Performance Expert', description: 'Optimization and profiling' },
      qa: { id: 'qa', name: 'QA Expert', description: 'Testing and quality assurance' },
      architect: { id: 'architect', name: 'System Architect', description: 'Overall system design' }
    };

    const persona = personas[personaId];
    if (!persona) {
      console.error('Unknown persona:', personaId);
      return false;
    }

    if (this.state.supervisors.find(s => s.id === personaId)) {
      console.warn('Supervisor already added:', personaId);
      return false;
    }

    const supervisor = {
      ...persona,
      addedAt: Date.now(),
      consulted: 0,
      lastConsulted: null
    };

    this.setState({
      supervisors: [...this.state.supervisors, supervisor]
    });

    this.logDecision({
      type: 'SUPERVISOR_ADDED',
      description: `Added ${persona.name} as supervisor`,
      result: 'APPROVED',
      timestamp: Date.now()
    });

    return true;
  }

  removeSupervisor(personaId) {
    const supervisor = this.state.supervisors.find(s => s.id === personaId);
    if (!supervisor) {
      console.warn('Supervisor not found:', personaId);
      return false;
    }

    this.setState({
      supervisors: this.state.supervisors.filter(s => s.id !== personaId)
    });

    this.logDecision({
      type: 'SUPERVISOR_REMOVED',
      description: `Removed ${supervisor.name} from supervision`,
      result: 'APPROVED',
      timestamp: Date.now()
    });

    return true;
  }

  // Autonomy level management
  setAutonomyLevel(level) {
    const validLevels = ['minimal', 'balanced', 'high'];
    if (!validLevels.includes(level)) {
      console.error('Invalid autonomy level:', level);
      return false;
    }

    this.setState({ autonomyLevel: level });
    
    this.logDecision({
      type: 'AUTONOMY_CHANGED',
      description: `Autonomy level changed to ${level}`,
      result: 'APPROVED',
      timestamp: Date.now()
    });

    return true;
  }

  // Decision processing
  async requestDecision(context) {
    if (!this.state.isEnabled) {
      return { approved: true, reason: 'Supervision disabled' };
    }

    if (this.state.supervisors.length === 0) {
      return { approved: true, reason: 'No supervisors available' };
    }

    const decision = {
      id: this.generateDecisionId(),
      context,
      timestamp: Date.now(),
      autonomyLevel: this.state.autonomyLevel,
      supervisors: [...this.state.supervisors]
    };

    // Check if decision should be auto-approved based on autonomy level
    if (this.shouldAutoApprove(context)) {
      const result = {
        approved: true,
        reason: 'Auto-approved based on autonomy level',
        decision
      };

      this.logDecision({
        type: context.type || 'DECISION',
        description: context.description || 'Decision requested',
        result: 'APPROVED',
        timestamp: Date.now(),
        details: result
      });

      return result;
    }

    // Queue decision for supervisor consultation
    return new Promise((resolve) => {
      this.decisionQueue.push({ decision, resolve });
      this.processDecisionQueue();
    });
  }

  generateDecisionId() {
    return 'decision_' + Date.now() + '_' + Math.random().toString(36).substr(2, 9);
  }

  shouldAutoApprove(context) {
    const { autonomyLevel } = this.state;
    const { severity = 'medium', type } = context;

    switch (autonomyLevel) {
      case 'high':
        return severity !== 'critical';
      case 'balanced':
        return severity === 'low';
      case 'minimal':
        return false;
      default:
        return false;
    }
  }

  async processDecisionQueue() {
    if (this.isProcessingDecision || this.decisionQueue.length === 0) {
      return;
    }

    this.isProcessingDecision = true;
    const { decision, resolve } = this.decisionQueue.shift();

    try {
      const result = await this.consultSupervisors(decision);
      resolve(result);
      
      this.logDecision({
        type: decision.context.type || 'DECISION',
        description: decision.context.description || 'Decision processed',
        result: result.approved ? 'APPROVED' : 'REJECTED',
        timestamp: Date.now(),
        details: result
      });
    } catch (error) {
      console.error('Error processing decision:', error);
      resolve({
        approved: false,
        reason: 'Error during supervision consultation',
        error: error.message
      });
    }

    this.isProcessingDecision = false;
    
    // Process next decision if any
    if (this.decisionQueue.length > 0) {
      setTimeout(() => this.processDecisionQueue(), 100);
    }
  }

  async consultSupervisors(decision) {
    const { context, supervisors } = decision;
    
    // Simulate supervisor consultation
    // In a real implementation, this would call AI services
    
    const relevantSupervisors = this.getRelevantSupervisors(context, supervisors);
    
    if (relevantSupervisors.length === 0) {
      return {
        approved: true,
        reason: 'No relevant supervisors for this decision',
        decision
      };
    }

    // Simulate async consultation
    await new Promise(resolve => setTimeout(resolve, 1000));

    // Mock decision logic
    const approval = Math.random() > 0.3; // 70% approval rate for demo
    
    // Update supervisor consultation stats
    relevantSupervisors.forEach(supervisor => {
      const index = this.state.supervisors.findIndex(s => s.id === supervisor.id);
      if (index !== -1) {
        this.state.supervisors[index].consulted++;
        this.state.supervisors[index].lastConsulted = Date.now();
      }
    });

    return {
      approved: approval,
      reason: approval ? 'Approved by supervisor consultation' : 'Rejected by supervisor consultation',
      consultedSupervisors: relevantSupervisors.map(s => s.name),
      decision
    };
  }

  getRelevantSupervisors(context, supervisors) {
    const { type, technologies = [] } = context;
    
    // Logic to determine which supervisors are relevant
    const relevanceMap = {
      'UI_CHANGE': ['frontend', 'qa'],
      'API_CHANGE': ['backend', 'security'],
      'PERFORMANCE': ['performance', 'architect'],
      'SECURITY': ['security', 'backend'],
      'TESTING': ['qa', 'frontend'],
      'ARCHITECTURE': ['architect', 'backend']
    };

    const relevantTypes = relevanceMap[type] || [];
    
    return supervisors.filter(supervisor => 
      relevantTypes.includes(supervisor.id) ||
      technologies.some(tech => 
        supervisor.description.toLowerCase().includes(tech.toLowerCase())
      )
    );
  }

  // Quality gate management
  updateQualityGate(gateName, score) {
    if (!this.state.qualityGates[gateName]) {
      console.error('Unknown quality gate:', gateName);
      return false;
    }

    const gates = { ...this.state.qualityGates };
    gates[gateName] = { ...gates[gateName], current: score };
    
    this.setState({ qualityGates: gates });
    
    // Check if gate threshold is met
    const gate = gates[gateName];
    if (gate.current >= gate.threshold) {
      this.logDecision({
        type: 'QUALITY_GATE_PASSED',
        description: `${gateName} quality gate passed (${gate.current}/${gate.threshold})`,
        result: 'APPROVED',
        timestamp: Date.now()
      });
    }

    return true;
  }

  // Decision logging
  logDecision(decision) {
    const decisions = [...this.state.decisions, decision];
    // Keep only last 50 decisions
    if (decisions.length > 50) {
      decisions.splice(0, decisions.length - 50);
    }
    
    this.setState({ decisions });
  }

  // Utility methods
  getSupervisionMetrics() {
    const { decisions, supervisors } = this.state;
    
    const totalDecisions = decisions.length;
    const approvedDecisions = decisions.filter(d => d.result === 'APPROVED').length;
    const approvalRate = totalDecisions > 0 ? (approvedDecisions / totalDecisions) * 100 : 0;
    
    const supervisorStats = supervisors.map(supervisor => ({
      ...supervisor,
      consultationRate: supervisor.consulted || 0
    }));

    return {
      totalDecisions,
      approvedDecisions,
      approvalRate: Math.round(approvalRate),
      supervisorStats,
      qualityGateStatus: Object.entries(this.state.qualityGates).map(([name, gate]) => ({
        name,
        passed: gate.current >= gate.threshold,
        score: gate.current,
        threshold: gate.threshold
      }))
    };
  }

  reset() {
    this.state = {
      isEnabled: false,
      autonomyLevel: 'balanced',
      supervisors: [],
      decisions: [],
      qualityGates: {
        security: { threshold: 8, current: 0 },
        performance: { threshold: 7, current: 0 },
        codeQuality: { threshold: 8, current: 0 }
      },
      sessionId: this.generateSessionId()
    };
    
    this.decisionQueue = [];
    this.isProcessingDecision = false;
    this.notifyListeners();
  }
}

// Singleton instance
let supervisionServiceInstance = null;

export const getSupervisionService = () => {
  if (!supervisionServiceInstance) {
    supervisionServiceInstance = new SupervisionService();
  }
  return supervisionServiceInstance;
};

export default SupervisionService;