import React, { useState, useEffect } from 'react';
import './SupervisionPage.css';

const SupervisionPage = ({ setCurrentPage }) => {
  const [supervisionState, setSupervisionState] = useState({
    isEnabled: false,
    autonomyLevel: 'balanced',
    supervisors: [],
    decisions: [],
    qualityGates: {
      security: { threshold: 8, current: 0 },
      performance: { threshold: 7, current: 0 },
      codeQuality: { threshold: 8, current: 0 }
    }
  });

  const [selectedPersona, setSelectedPersona] = useState('');
  const [isAddingPersona, setIsAddingPersona] = useState(false);

  const personas = [
    { id: 'frontend', name: 'Frontend Expert', description: 'UI/UX and React expertise' },
    { id: 'backend', name: 'Backend Expert', description: 'API and server architecture' },
    { id: 'security', name: 'Security Expert', description: 'Security auditing and best practices' },
    { id: 'performance', name: 'Performance Expert', description: 'Optimization and profiling' },
    { id: 'qa', name: 'QA Expert', description: 'Testing and quality assurance' },
    { id: 'architect', name: 'System Architect', description: 'Overall system design' }
  ];

  const autonomyLevels = [
    { value: 'minimal', label: 'Minimal Autonomy', description: 'Ask before every action' },
    { value: 'balanced', label: 'Balanced', description: 'Ask for significant decisions' },
    { value: 'high', label: 'High Autonomy', description: 'Only ask for critical decisions' }
  ];

  const enableSupervision = () => {
    setSupervisionState(prev => ({ ...prev, isEnabled: true }));
  };

  const disableSupervision = () => {
    setSupervisionState(prev => ({ 
      ...prev, 
      isEnabled: false, 
      supervisors: [],
      decisions: []
    }));
  };

  const addSupervisor = (personaId) => {
    const persona = personas.find(p => p.id === personaId);
    if (persona && !supervisionState.supervisors.find(s => s.id === personaId)) {
      setSupervisionState(prev => ({
        ...prev,
        supervisors: [...prev.supervisors, { ...persona, addedAt: Date.now() }]
      }));
      setSelectedPersona('');
      setIsAddingPersona(false);
    }
  };

  const removeSupervisor = (personaId) => {
    setSupervisionState(prev => ({
      ...prev,
      supervisors: prev.supervisors.filter(s => s.id !== personaId)
    }));
  };

  const updateAutonomyLevel = (level) => {
    setSupervisionState(prev => ({ ...prev, autonomyLevel: level }));
  };

  const getQualityGateColor = (gate) => {
    const ratio = gate.current / gate.threshold;
    if (ratio >= 1) return '#10b981'; // green
    if (ratio >= 0.7) return '#f59e0b'; // yellow
    return '#ef4444'; // red
  };

  return (
    <div className="supervision-page">
      <div className="supervision-header">
        <div className="supervision-header-top">
          <button 
            className="back-to-ide-btn"
            onClick={() => setCurrentPage('ide')}
            title="Back to IDE"
          >
            ← Back to IDE
          </button>
        </div>
        <h1>Claude Code Supervision Engine</h1>
        <p>Monitor and guide autonomous development with AI expert consultation</p>
      </div>

      {/* Status Panel */}
      <div className="supervision-panel">
        <div className="panel-header">
          <h2>Supervision Status</h2>
          <div className="status-controls">
            {!supervisionState.isEnabled ? (
              <button 
                className="btn btn-primary"
                onClick={enableSupervision}
              >
                Enable Supervision
              </button>
            ) : (
              <button 
                className="btn btn-danger"
                onClick={disableSupervision}
              >
                Disable Supervision
              </button>
            )}
          </div>
        </div>
        
        <div className="status-grid">
          <div className="status-item">
            <span className="status-label">Status:</span>
            <span className={`status-value ${supervisionState.isEnabled ? 'active' : 'inactive'}`}>
              {supervisionState.isEnabled ? 'Active' : 'Inactive'}
            </span>
          </div>
          <div className="status-item">
            <span className="status-label">Autonomy Level:</span>
            <span className="status-value">{supervisionState.autonomyLevel}</span>
          </div>
          <div className="status-item">
            <span className="status-label">Active Supervisors:</span>
            <span className="status-value">{supervisionState.supervisors.length}</span>
          </div>
        </div>
      </div>

      {/* Autonomy Level Configuration */}
      {supervisionState.isEnabled && (
        <div className="supervision-panel">
          <h2>Autonomy Configuration</h2>
          <div className="autonomy-levels">
            {autonomyLevels.map(level => (
              <div 
                key={level.value}
                className={`autonomy-option ${supervisionState.autonomyLevel === level.value ? 'selected' : ''}`}
                onClick={() => updateAutonomyLevel(level.value)}
              >
                <div className="autonomy-title">{level.label}</div>
                <div className="autonomy-description">{level.description}</div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Expert Supervisors */}
      {supervisionState.isEnabled && (
        <div className="supervision-panel">
          <div className="panel-header">
            <h2>Expert Supervisors</h2>
            <button 
              className="btn btn-secondary"
              onClick={() => setIsAddingPersona(true)}
              disabled={isAddingPersona}
            >
              Add Supervisor
            </button>
          </div>

          {/* Add Persona Modal */}
          {isAddingPersona && (
            <div className="add-persona-modal">
              <h3>Select Expert Supervisor</h3>
              <div className="persona-grid">
                {personas
                  .filter(p => !supervisionState.supervisors.find(s => s.id === p.id))
                  .map(persona => (
                    <div 
                      key={persona.id}
                      className="persona-card"
                      onClick={() => addSupervisor(persona.id)}
                    >
                      <div className="persona-name">{persona.name}</div>
                      <div className="persona-description">{persona.description}</div>
                    </div>
                  ))
                }
              </div>
              <button 
                className="btn btn-secondary"
                onClick={() => setIsAddingPersona(false)}
              >
                Cancel
              </button>
            </div>
          )}

          {/* Active Supervisors */}
          <div className="supervisors-list">
            {supervisionState.supervisors.length === 0 ? (
              <div className="empty-state">
                <p>No supervisors active. Add an expert to begin supervision.</p>
              </div>
            ) : (
              supervisionState.supervisors.map(supervisor => (
                <div key={supervisor.id} className="supervisor-card">
                  <div className="supervisor-info">
                    <div className="supervisor-name">{supervisor.name}</div>
                    <div className="supervisor-description">{supervisor.description}</div>
                    <div className="supervisor-meta">
                      Added: {new Date(supervisor.addedAt).toLocaleTimeString()}
                    </div>
                  </div>
                  <button 
                    className="btn btn-danger btn-small"
                    onClick={() => removeSupervisor(supervisor.id)}
                    title="Remove supervisor"
                  >
                    Remove
                  </button>
                </div>
              ))
            )}
          </div>
        </div>
      )}

      {/* Quality Gates */}
      {supervisionState.isEnabled && (
        <div className="supervision-panel">
          <h2>Quality Gates</h2>
          <div className="quality-gates">
            {Object.entries(supervisionState.qualityGates).map(([key, gate]) => (
              <div key={key} className="quality-gate">
                <div className="gate-header">
                  <span className="gate-name">{key.charAt(0).toUpperCase() + key.slice(1)}</span>
                  <span className="gate-score">{gate.current}/{gate.threshold}</span>
                </div>
                <div className="gate-progress">
                  <div 
                    className="gate-progress-bar"
                    style={{ 
                      width: `${Math.min(100, (gate.current / gate.threshold) * 100)}%`,
                      backgroundColor: getQualityGateColor(gate)
                    }}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Recent Decisions */}
      {supervisionState.isEnabled && supervisionState.decisions.length > 0 && (
        <div className="supervision-panel">
          <h2>Recent Decisions</h2>
          <div className="decisions-list">
            {supervisionState.decisions.slice(-5).map((decision, index) => (
              <div key={index} className="decision-item">
                <div className="decision-header">
                  <span className="decision-type">{decision.type}</span>
                  <span className="decision-time">{new Date(decision.timestamp).toLocaleTimeString()}</span>
                </div>
                <div className="decision-description">{decision.description}</div>
                <div className="decision-result">
                  Result: <span className={`result-${decision.result.toLowerCase()}`}>{decision.result}</span>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default SupervisionPage;