import React, { useState } from 'react';
import './FileExplorer.css';
import { useSupervision } from './useSupervision';

const FileExplorer = ({ 
  fileStructure, 
  onFileSelect, 
  activeFile, 
  onReactBitsCommand,
  // New props for controls
  currentPage,
  setCurrentPage,
  onMenuAction
}) => {
  const [expandedFolders, setExpandedFolders] = useState(new Set(['project-root', 'src']));
  const { isEnabled: supervisionEnabled } = useSupervision();

  const toggleFolder = (folderName) => {
    const newExpanded = new Set(expandedFolders);
    if (newExpanded.has(folderName)) {
      newExpanded.delete(folderName);
    } else {
      newExpanded.add(folderName);
    }
    setExpandedFolders(newExpanded);
  };

  const getFileIcon = (file) => {
    if (file.type === 'folder') {
      return expandedFolders.has(file.name) ? '📂' : '📁';
    }
    
    const ext = file.name.split('.').pop().toLowerCase();
    switch (ext) {
      case 'js':
      case 'jsx':
        return '📄';
      case 'ts':
      case 'tsx':
        return '📘';
      case 'css':
        return '🎨';
      case 'json':
        return '⚙️';
      case 'md':
        return '📝';
      case 'html':
        return '🌐';
      default:
        return '📄';
    }
  };

  const renderFileTree = (node, depth = 0) => {
    const isExpanded = expandedFolders.has(node.name);
    const isActive = activeFile && activeFile.name === node.name;
    
    return (
      <div key={node.name} className="file-tree-item">
        <div 
          className={`file-item ${isActive ? 'active' : ''} ${node.isGenerated ? 'generated' : ''}`}
          style={{ paddingLeft: `${depth * 16 + 8}px` }}
          onClick={() => {
            if (node.type === 'folder') {
              toggleFolder(node.name);
            } else {
              onFileSelect(node);
            }
          }}
        >
          <span className="file-icon">{getFileIcon(node)}</span>
          <span className="file-name">{node.name}</span>
          {node.isGenerated && <span className="generated-badge">✨</span>}
        </div>
        
        {node.type === 'folder' && isExpanded && node.children && (
          <div className="folder-contents">
            {node.children.map(child => renderFileTree(child, depth + 1))}
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="file-explorer">
      <div className="explorer-header">
        <h3>Explorer</h3>
        <div className="explorer-actions">
          <button className="explorer-btn" title="New File">
            📄
          </button>
          <button className="explorer-btn" title="New Folder">
            📁
          </button>
          <button className="explorer-btn" title="Refresh">
            🔄
          </button>
        </div>
      </div>
      
      <div className="explorer-content">
        <div className="project-section">
          <div className="section-header">
            <span className="section-icon">🚀</span>
            <span className="section-title">Project Files</span>
          </div>
          <div className="file-tree">
            {renderFileTree(fileStructure)}
          </div>
        </div>
      </div>
      
      <div className="controls-section">
        <div className="section-header">
          <span className="section-icon">⚙️</span>
          <span className="section-title">Settings</span>
        </div>
        <div className="controls-buttons">
          <button 
            className="control-sidebar-btn" 
            title="IDE Settings"
            onClick={() => onMenuAction('showSettings')}
          >
            <span className="control-icon">⚙️</span>
            <span className="control-label">IDE Settings</span>
          </button>
        </div>
      </div>
      
      <div className="react-bits-section">
        <div className="section-header">
          <span className="section-icon">✨</span>
          <span className="section-title">React Bits</span>
        </div>
        <div className="react-bits-info">
          <p>Generate React components using natural language:</p>
          <div className="react-bits-commands">
            <button 
              className="react-bits-btn"
              onClick={() => onReactBitsCommand && onReactBitsCommand('/ui create button')}
            >
              <code>/ui create button</code>
            </button>
            <button 
              className="react-bits-btn"
              onClick={() => onReactBitsCommand && onReactBitsCommand('/ui make card')}
            >
              <code>/ui make card</code>
            </button>
            <button 
              className="react-bits-btn"
              onClick={() => onReactBitsCommand && onReactBitsCommand('/ui build loader')}
            >
              <code>/ui build loader</code>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FileExplorer;