import React, { useState, useEffect, useRef } from 'react';
import Editor from '@monaco-editor/react';
import './CodeEditor.css';

const CodeEditor = ({ activeFile, openFiles, onFileClose, onFileSelect }) => {
  const editorRef = useRef(null);
  const [fileContents, setFileContents] = useState({});

  const handleEditorDidMount = (editor, monaco) => {
    editorRef.current = editor;
    
    // Configure Tokyo Night theme
    monaco.editor.defineTheme('tokyo-night', {
      base: 'vs-dark',
      inherit: true,
      rules: [
        { token: 'comment', foreground: '565f89', fontStyle: 'italic' },
        { token: 'keyword', foreground: 'bb9af7' },
        { token: 'operator', foreground: '89ddff' },
        { token: 'namespace', foreground: '7aa2f7' },
        { token: 'type', foreground: '2ac3de' },
        { token: 'struct', foreground: '2ac3de' },
        { token: 'class', foreground: '2ac3de' },
        { token: 'interface', foreground: '2ac3de' },
        { token: 'parameter', foreground: 'e0af68' },
        { token: 'variable', foreground: 'c0caf5' },
        { token: 'property', foreground: '7aa2f7' },
        { token: 'enumMember', foreground: '2ac3de' },
        { token: 'function', foreground: '7aa2f7' },
        { token: 'member', foreground: '7aa2f7' },
        { token: 'macro', foreground: 'bb9af7' },
        { token: 'string', foreground: '9ece6a' },
        { token: 'number', foreground: 'ff9e64' },
        { token: 'regexp', foreground: 'b4f9f8' },
        { token: 'delimiter', foreground: 'c0caf5' }
      ],
      colors: {
        'editor.background': '#1a1b26',
        'editor.foreground': '#c0caf5',
        'editorLineNumber.foreground': '#565f89',
        'editorLineNumber.activeForeground': '#c0caf5',
        'editor.selectionBackground': '#28344a',
        'editor.selectionHighlightBackground': '#1f2335',
        'editor.findMatchBackground': '#3d59a1',
        'editor.findMatchHighlightBackground': '#28344a',
        'editor.lineHighlightBackground': '#1f2335',
        'editorCursor.foreground': '#c0caf5',
        'editorWhitespace.foreground': '#414868',
        'editorIndentGuide.background': '#414868',
        'editorIndentGuide.activeBackground': '#565f89'
      }
    });
    
    monaco.editor.setTheme('tokyo-night');
  };

  const getLanguage = (fileName) => {
    const ext = fileName.split('.').pop().toLowerCase();
    switch (ext) {
      case 'js':
      case 'jsx':
        return 'javascript';
      case 'ts':
      case 'tsx':
        return 'typescript';
      case 'css':
        return 'css';
      case 'json':
        return 'json';
      case 'md':
        return 'markdown';
      case 'html':
        return 'html';
      case 'py':
        return 'python';
      default:
        return 'plaintext';
    }
  };

  const handleEditorChange = (value) => {
    if (activeFile) {
      setFileContents(prev => ({
        ...prev,
        [activeFile.name]: value
      }));
    }
  };

  const getCurrentContent = () => {
    if (!activeFile) return '';
    return fileContents[activeFile.name] !== undefined 
      ? fileContents[activeFile.name] 
      : activeFile.content || '';
  };

  useEffect(() => {
    // Initialize file contents for all open files
    const newContents = {};
    openFiles.forEach(file => {
      if (fileContents[file.name] === undefined) {
        newContents[file.name] = file.content || '';
      }
    });
    if (Object.keys(newContents).length > 0) {
      setFileContents(prev => ({ ...prev, ...newContents }));
    }
  }, [openFiles]);

  const hasUnsavedChanges = (file) => {
    return fileContents[file.name] !== undefined && 
           fileContents[file.name] !== (file.content || '');
  };

  if (openFiles.length === 0) {
    return (
      <div className="code-editor no-files">
        <div className="welcome-screen">
          <h2>🚀 Welcome to Coder1 IDE</h2>
          <p>Select a file from the explorer to start editing</p>
          <div className="quick-actions">
            <button className="quick-action">
              📄 Create New File
            </button>
            <button className="quick-action">
              📁 Open Folder
            </button>
          </div>
          <div className="react-bits-info">
            <h3>✨ React Bits Integration</h3>
            <p>Generate React components using the terminal:</p>
            <ul>
              <li><code>/ui create a button</code></li>
              <li><code>/ui make a card</code></li>
              <li><code>/ui build a loader</code></li>
            </ul>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="code-editor">
      <div className="editor-tabs">
        {openFiles.map(file => (
          <div 
            key={file.name}
            className={`editor-tab ${activeFile && activeFile.name === file.name ? 'active' : ''}`}
            onClick={() => onFileSelect(file)}
          >
            <span className="tab-icon">
              {file.isGenerated ? '✨' : getLanguage(file.name) === 'javascript' ? '📄' : 
               getLanguage(file.name) === 'typescript' ? '📘' : '📄'}
            </span>
            <span className="tab-name">{file.name}</span>
            {hasUnsavedChanges(file) && <span className="unsaved-indicator">●</span>}
            <button 
              className="tab-close"
              onClick={(e) => {
                e.stopPropagation();
                onFileClose(file.name);
              }}
            >
              ✕
            </button>
          </div>
        ))}
      </div>
      
      <div className="editor-content">
        {activeFile && (
          <Editor
            height="100%"
            language={getLanguage(activeFile.name)}
            value={getCurrentContent()}
            onChange={handleEditorChange}
            onMount={handleEditorDidMount}
            options={{
              fontSize: 14,
              fontFamily: 'Monaco, Menlo, "Ubuntu Mono", monospace',
              minimap: { enabled: true },
              wordWrap: 'on',
              lineNumbers: 'on',
              folding: true,
              lineDecorationsWidth: 0,
              lineNumbersMinChars: 3,
              renderLineHighlight: 'line',
              selectOnLineNumbers: true,
              automaticLayout: true,
              scrollBeyondLastLine: false,
              contextmenu: true,
              cursorStyle: 'line',
              cursorBlinking: 'blink',
              renderWhitespace: 'selection',
              tabSize: 2,
              insertSpaces: true
            }}
          />
        )}
      </div>
    </div>
  );
};

export default CodeEditor;