import React, { useState, useRef } from 'react';
import './Header.css';
import MenuDropdown from './MenuDropdown';
import { getMenuItems } from './MenuItems';
const Header = ({ 
  explorerVisible, 
  previewVisible, 
  terminalVisible, 
  onToggleExplorer, 
  onTogglePreview, 
  onToggleTerminal,
  reactBitsEnabled,
  // Menu callbacks
  onMenuAction,
  // Page context
  currentPage,
  setCurrentPage
}) => {
  const [activeMenu, setActiveMenu] = useState(null);
  const menuRefs = useRef({});

  const handleMenuClick = (menuName) => {
    setActiveMenu(activeMenu === menuName ? null : menuName);
  };

  const handleMenuClose = () => {
    setActiveMenu(null);
  };

  // Create menu action callbacks
  const menuCallbacks = {
    onNewFile: () => onMenuAction('newFile'),
    onOpenFile: () => onMenuAction('openFile'),
    onSaveFile: () => onMenuAction('saveFile'),
    onSaveAsFile: () => onMenuAction('saveAsFile'),
    onToggleExplorer: () => {
      onToggleExplorer();
      handleMenuClose();
    },
    onTogglePreview: () => {
      onTogglePreview();
      handleMenuClose();
    },
    onToggleTerminal: () => {
      onToggleTerminal();
      handleMenuClose();
    },
    onClearTerminal: () => onMenuAction('clearTerminal'),
    onUndo: () => onMenuAction('undo'),
    onRedo: () => onMenuAction('redo'),
    onCut: () => onMenuAction('cut'),
    onCopy: () => onMenuAction('copy'),
    onPaste: () => onMenuAction('paste'),
    onFind: () => onMenuAction('find'),
    onReplace: () => onMenuAction('replace'),
    onZoomIn: () => onMenuAction('zoomIn'),
    onZoomOut: () => onMenuAction('zoomOut'),
    onToggleFullScreen: () => onMenuAction('toggleFullScreen'),
    onToggleTheme: () => onMenuAction('toggleTheme'),
    onShowDocumentation: () => onMenuAction('showDocumentation'),
    onShowShortcuts: () => onMenuAction('showShortcuts'),
    onShowAbout: () => onMenuAction('showAbout'),
    onShowSuperClaudeGuide: () => onMenuAction('showSuperClaudeGuide'),
    onShowReactBitsGuide: () => onMenuAction('showReactBitsGuide'),
    onSuperClaudeCommand: (command) => {
      onMenuAction('superClaudeCommand', command);
      handleMenuClose();
    },
    onReactBitsCommand: (command) => {
      onMenuAction('reactBitsCommand', command);
      handleMenuClose();
    }
  };

  const menuItems = getMenuItems(menuCallbacks);
  const menuNames = ['File', 'Edit', 'View', 'Terminal', 'Help'];

  return (
    <header className="header">
      <div className="header-left">
        <div className="logo">
          {currentPage === 'ide' ? (
            <a 
              href="http://localhost:3000" 
              className="logo-link"
              title="Back to Coder1 Main Page"
            >
              <img 
                src="/coder1-logo.svg" 
                alt="Coder1 IDE" 
                className="logo-image"
              />
            </a>
          ) : (
            <button 
              className="logo-link logo-btn"
              onClick={() => setCurrentPage('ide')}
              title="Back to IDE"
            >
              <img 
                src="/coder1-logo.svg" 
                alt="Coder1 IDE" 
                className="logo-image"
              />
            </button>
          )}
        </div>
      </div>
      
      <div className="header-center">
        <nav className="header-nav">
          {menuNames.map((menuName) => (
            <div key={menuName} className="nav-item-container">
              <button 
                ref={el => menuRefs.current[menuName] = el}
                className={`nav-item ${activeMenu === menuName ? 'active' : ''}`}
                onClick={() => handleMenuClick(menuName)}
              >
                {menuName}
              </button>
              <MenuDropdown
                isOpen={activeMenu === menuName}
                onClose={handleMenuClose}
                items={menuItems[menuName] || []}
                position="left"
              />
            </div>
          ))}
        </nav>
      </div>
      
      <div className="header-right">
        <div className="view-controls">
          <button 
            className={`control-btn ${explorerVisible ? 'active' : ''}`}
            onClick={onToggleExplorer}
            title="Toggle File Explorer"
          >
            <span className="control-icon">📂</span>
            <span className="control-label">Explorer</span>
          </button>
          <button 
            className={`control-btn ${terminalVisible ? 'active' : ''}`}
            onClick={onToggleTerminal}
            title="Toggle Terminal"
          >
            <span className="control-icon">▶️</span>
            <span className="control-label">Terminal</span>
          </button>
          <button 
            className={`control-btn ${previewVisible ? 'active' : ''}`}
            onClick={onTogglePreview}
            title="Toggle Preview"
          >
            <span className="control-icon">🖼️</span>
            <span className="control-label">Preview</span>
          </button>
        </div>
        
        <div className="header-actions">
          <button 
            className="action-btn superclaude-btn" 
            title="SuperClaude AI Assistant"
            onClick={() => onMenuAction('showSuperClaudeGuide')}
          >
            🤖
          </button>
          <button 
            className="action-btn" 
            title="New File"
            onClick={() => onMenuAction('newFile')}
          >
            ➕
          </button>
          <button 
            className="action-btn" 
            title="Save"
            onClick={() => onMenuAction('saveFile')}
          >
            💾
          </button>
        </div>
        
        <div className="connection-status">
          <span className="status-dot connected"></span>
          <span className="status-text">Connected</span>
        </div>
      </div>
    </header>
  );
};

export default Header;