import React, { useState, useEffect } from 'react';
import ClaudeAPI from '../services/ClaudeAPI';
import './SettingsModal.css';

const SettingsModal = ({ isOpen, onClose }) => {
  const [apiKey, setApiKey] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [testResult, setTestResult] = useState(null);
  const [showApiKey, setShowApiKey] = useState(false);

  useEffect(() => {
    if (isOpen) {
      // Load existing API key from localStorage
      const existingKey = ClaudeAPI.getUserApiKey();
      if (existingKey) {
        setApiKey(existingKey);
      }
    }
  }, [isOpen]);

  const handleSaveApiKey = () => {
    if (apiKey.trim()) {
      ClaudeAPI.setUserApiKey(apiKey.trim());
      setTestResult({ success: true, message: 'API key saved successfully!' });
      setTimeout(() => {
        setTestResult(null);
      }, 3000);
    }
  };

  const handleTestApiKey = async () => {
    if (!apiKey.trim()) {
      setTestResult({ success: false, message: 'Please enter an API key first.' });
      return;
    }

    setIsLoading(true);
    setTestResult(null);

    try {
      const response = await ClaudeAPI.sendMessage([{
        role: 'user',
        content: 'Hello! Please respond with just "API test successful" to confirm the connection.'
      }], 'You are a helpful assistant. Respond concisely.', 50, apiKey.trim());

      if (response && response.includes('API test successful')) {
        setTestResult({ success: true, message: 'API key is valid! Connection successful.' });
        ClaudeAPI.setUserApiKey(apiKey.trim());
      } else {
        setTestResult({ success: true, message: 'API key works, but got unexpected response.' });
        ClaudeAPI.setUserApiKey(apiKey.trim());
      }
    } catch (error) {
      console.error('API test failed:', error);
      setTestResult({ 
        success: false, 
        message: `API test failed: ${error.message}` 
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleClearApiKey = () => {
    setApiKey('');
    ClaudeAPI.setUserApiKey('');
    setTestResult({ success: true, message: 'API key cleared.' });
    setTimeout(() => {
      setTestResult(null);
    }, 2000);
  };

  if (!isOpen) return null;

  return (
    <div className="settings-modal-overlay" onClick={onClose}>
      <div className="settings-modal" onClick={e => e.stopPropagation()}>
        <div className="settings-header">
          <h2>⚙️ IDE Settings</h2>
          <button className="close-btn" onClick={onClose}>✕</button>
        </div>
        
        <div className="settings-content">
          <div className="settings-section">
            <h3>🤖 Claude API Configuration</h3>
            <p className="settings-description">
              Configure your Claude API key to enable SuperClaude AI features and live code assistance.
            </p>
            
            <div className="api-key-input-group">
              <label htmlFor="apiKey">Claude API Key:</label>
              <div className="input-with-toggle">
                <input
                  id="apiKey"
                  type={showApiKey ? 'text' : 'password'}
                  value={apiKey}
                  onChange={(e) => setApiKey(e.target.value)}
                  placeholder="sk-ant-api03-..."
                  className="api-key-input"
                />
                <button 
                  type="button"
                  className="toggle-visibility-btn"
                  onClick={() => setShowApiKey(!showApiKey)}
                  title={showApiKey ? 'Hide API key' : 'Show API key'}
                >
                  {showApiKey ? '🙈' : '👁️'}
                </button>
              </div>
            </div>

            <div className="settings-actions">
              <button 
                className="btn btn-primary"
                onClick={handleSaveApiKey}
                disabled={!apiKey.trim()}
              >
                💾 Save API Key
              </button>
              
              <button 
                className="btn btn-secondary"
                onClick={handleTestApiKey}
                disabled={!apiKey.trim() || isLoading}
              >
                {isLoading ? '🔄 Testing...' : '🧪 Test Connection'}
              </button>
              
              <button 
                className="btn btn-danger"
                onClick={handleClearApiKey}
              >
                🗑️ Clear
              </button>
            </div>

            {testResult && (
              <div className={`test-result ${testResult.success ? 'success' : 'error'}`}>
                <span className="result-icon">
                  {testResult.success ? '✅' : '❌'}
                </span>
                <span className="result-message">{testResult.message}</span>
              </div>
            )}

            <div className="api-key-info">
              <h4>📋 How to get your Claude API key:</h4>
              <ol>
                <li>Visit <a href="https://console.anthropic.com/" target="_blank" rel="noopener noreferrer">console.anthropic.com</a></li>
                <li>Sign up or log in to your account</li>
                <li>Navigate to API Keys section</li>
                <li>Create a new API key</li>
                <li>Copy and paste it above</li>
              </ol>
              
              <div className="api-key-status">
                <strong>Current Status:</strong>
                <span className={`status-badge ${ClaudeAPI.hasValidApiKey() ? 'connected' : 'disconnected'}`}>
                  {ClaudeAPI.hasValidApiKey() ? '🟢 Connected' : '🔴 Not Connected'}
                </span>
              </div>
            </div>
          </div>

          <div className="settings-section">
            <h3>🔧 IDE Features</h3>
            <div className="feature-info">
              <div className="feature-item">
                <strong>SuperClaude Commands:</strong>
                <p>Use /analyze, /build, /test, /security commands with AI assistance</p>
              </div>
              <div className="feature-item">
                <strong>ReactBits:</strong>
                <p>Generate React components with /ui commands</p>
              </div>
              <div className="feature-item">
                <strong>Terminal Integration:</strong>
                <p>Full Claude Code CLI integration coming soon</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsModal;