import React, { useState, useEffect } from 'react';
import './SleepModeIndicator.css';

const SleepModeIndicator = ({ status, onClick }) => {
  const [isVisible, setIsVisible] = useState(false);
  const [runtime, setRuntime] = useState('0h 0m');

  useEffect(() => {
    setIsVisible(!!status?.isActive);
  }, [status]);

  useEffect(() => {
    if (status?.isActive && status.startTime) {
      const updateRuntime = () => {
        const now = new Date();
        const diff = now - status.startTime;
        const hours = Math.floor(diff / (1000 * 60 * 60));
        const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
        setRuntime(`${hours}h ${minutes}m`);
      };

      updateRuntime();
      const interval = setInterval(updateRuntime, 60000); // Update every minute
      return () => clearInterval(interval);
    }
  }, [status]);

  if (!isVisible) return null;

  const getStatusColor = () => {
    if (status?.isActive) {
      return '#4ade80'; // Green for active
    }
    return '#6b7280'; // Gray for inactive
  };

  const getStatusIcon = () => {
    if (status?.isActive) {
      return '🌙';
    }
    return '⏸️';
  };

  const getStatusText = () => {
    if (status?.isActive) {
      return 'Sleep Mode Active';
    }
    return 'Sleep Mode Paused';
  };

  const getEfficiencyRate = () => {
    if (!status) return 0;
    const total = status.changesApproved + status.changesRejected;
    if (total === 0) return 100;
    return Math.round((status.changesApproved / total) * 100);
  };

  return (
    <div 
      className={`sleep-mode-indicator ${status?.isActive ? 'active' : 'paused'}`}
      onClick={onClick}
      style={{ borderLeftColor: getStatusColor() }}
    >
      <div className="indicator-header">
        <span className="indicator-icon">{getStatusIcon()}</span>
        <span className="indicator-text">{getStatusText()}</span>
        <span className="indicator-runtime">{runtime}</span>
      </div>
      
      <div className="indicator-stats">
        <div className="mini-stat">
          <span className="mini-stat-value approved">{status?.changesApproved || 0}</span>
          <span className="mini-stat-label">Approved</span>
        </div>
        <div className="mini-stat">
          <span className="mini-stat-value rejected">{status?.changesRejected || 0}</span>
          <span className="mini-stat-label">Rejected</span>
        </div>
        <div className="mini-stat">
          <span className="mini-stat-value efficiency">{getEfficiencyRate()}%</span>
          <span className="mini-stat-label">Efficiency</span>
        </div>
      </div>

      <div className="indicator-pulse"></div>
    </div>
  );
};

export default SleepModeIndicator;