import React, { useState, useRef, useEffect } from 'react';
import './Terminal.css';

// SuperClaude Commands Configuration
const SUPER_CLAUDE_COMMANDS = {
  '/analyze': {
    name: '/analyze',
    description: 'Comprehensive code analysis with AI insights',
    personas: ['frontend', 'backend', 'analyzer', 'security'],
    flags: ['--security', '--performance', '--bugs', '--style'],
    examples: ['/analyze --security', '/analyze --performance --bugs']
  },
  '/build': {
    name: '/build',
    description: 'AI-assisted feature development',
    personas: ['frontend', 'backend', 'architect'],
    flags: ['--tdd', '--responsive', '--accessible'],
    examples: ['/build --frontend --tdd', '/build user dashboard']
  },
  '/test': {
    name: '/test',
    description: 'Generate and run comprehensive tests',
    personas: ['qa', 'backend', 'frontend'],
    flags: ['--unit', '--integration', '--e2e'],
    examples: ['/test --unit', '/test --e2e --qa']
  },
  '/security': {
    name: '/security',
    description: 'Security audit and vulnerability assessment',
    personas: ['security', 'backend'],
    flags: ['--owasp', '--audit', '--scan'],
    examples: ['/security --owasp', '/security --audit --scan']
  },
  '/improve': {
    name: '/improve',
    description: 'Code optimization and enhancement suggestions',
    personas: ['performance', 'refactorer', 'architect'],
    flags: ['--performance', '--refactor', '--optimize'],
    examples: ['/improve --performance', '/improve --refactor']
  },
  '/troubleshoot': {
    name: '/troubleshoot',
    description: 'Debug and solve development issues',
    personas: ['analyzer', 'backend', 'qa'],
    flags: ['--debug', '--trace', '--analyze'],
    examples: ['/troubleshoot --debug', '/troubleshoot error handling']
  },
  '/explain': {
    name: '/explain',
    description: 'Explain code functionality and concepts',
    personas: ['mentor', 'architect'],
    flags: ['--detailed', '--beginner', '--examples'],
    examples: ['/explain --detailed', '/explain React hooks']
  },
  '/design': {
    name: '/design',
    description: 'System design and architecture planning',
    personas: ['architect', 'frontend'],
    flags: ['--scalable', '--microservices', '--patterns'],
    examples: ['/design --scalable', '/design user authentication']
  },
  '/infinite': {
    name: '/infinite',
    description: 'Infinite agentic loop - generate unlimited self-contained solutions',
    personas: ['architect', 'frontend', 'backend'],
    flags: ['--spec', '--output', '--count'],
    examples: ['/infinite', '/infinite specs/ui-spec-v3.md source_infinite infinite', '/infinite custom-spec.md output_dir 20']
  }
};

const CLAUDE_PERSONAS = {
  frontend: { name: 'Frontend Expert', emoji: '🎨', description: 'UI/UX and React specialist' },
  backend: { name: 'Backend Expert', emoji: '⚙️', description: 'API and server architecture' },
  security: { name: 'Security Expert', emoji: '🔒', description: 'Security and compliance' },
  qa: { name: 'QA Expert', emoji: '🧪', description: 'Testing and quality assurance' },
  performance: { name: 'Performance Expert', emoji: '⚡', description: 'Optimization specialist' },
  architect: { name: 'Solution Architect', emoji: '🏗️', description: 'System design thinking' },
  analyzer: { name: 'Code Analyzer', emoji: '🔍', description: 'Root cause investigation' },
  refactorer: { name: 'Refactoring Expert', emoji: '🔧', description: 'Code improvement' },
  mentor: { name: 'Code Mentor', emoji: '👨‍🏫', description: 'Teaching and documentation' }
};

const TerminalEnhanced = ({ 
  output, 
  onCommand, 
  onSuperClaudeCommand, 
  reactBitsEnabled,
  // Page navigation props
  currentPage,
  setCurrentPage,
  supervisionEnabled = false,
  onMenuAction
}) => {
  const [currentCommand, setCurrentCommand] = useState('');
  const [commandHistory, setCommandHistory] = useState([]);
  const [historyIndex, setHistoryIndex] = useState(-1);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [isFocused, setIsFocused] = useState(true);
  
  // SuperClaude specific state
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [suggestions, setSuggestions] = useState([]);
  const [selectedSuggestion, setSelectedSuggestion] = useState(0);
  const [selectedPersona, setSelectedPersona] = useState('frontend');
  const [showPersonaSelector, setShowPersonaSelector] = useState(false);
  const [isExecuting, setIsExecuting] = useState(false);
  
  const terminalRef = useRef(null);
  const containerRef = useRef(null);

  useEffect(() => {
    // Auto-scroll to bottom when new output is added
    if (terminalRef.current) {
      terminalRef.current.scrollTop = terminalRef.current.scrollHeight;
    }
  }, [output]);

  useEffect(() => {
    // Set up global keyboard event listener for native terminal input
    const handleGlobalKeyDown = (e) => {
      // Don't interfere with form inputs or when suggestions are open with navigation
      if (e.target.tagName === 'INPUT' || e.target.tagName === 'TEXTAREA') return;
      if (showPersonaSelector) return;
      
      // Focus terminal and handle typing
      setIsFocused(true);
      
      if (e.key.length === 1 && !e.ctrlKey && !e.metaKey && !e.altKey) {
        // Regular character typing
        e.preventDefault();
        setCurrentCommand(prev => prev + e.key);
        const newInput = currentCommand + e.key;
        handleInputChange(newInput);
      } else if (e.key === 'Backspace') {
        e.preventDefault();
        setCurrentCommand(prev => prev.slice(0, -1));
        const newInput = currentCommand.slice(0, -1);
        handleInputChange(newInput);
      } else if (e.key === 'Enter') {
        e.preventDefault();
        handleSubmit(e);
      } else {
        // Handle special keys (arrows, etc.)
        handleKeyDown(e);
      }
    };

    // Add event listener to document
    document.addEventListener('keydown', handleGlobalKeyDown);
    
    // Cleanup on unmount
    return () => {
      document.removeEventListener('keydown', handleGlobalKeyDown);
    };
  }, [currentCommand, showSuggestions, suggestions, selectedSuggestion, commandHistory, historyIndex, showPersonaSelector]);

  // Handle terminal focus
  useEffect(() => {
    const handleClick = (e) => {
      if (containerRef.current?.contains(e.target)) {
        setIsFocused(true);
      }
    };
    
    document.addEventListener('click', handleClick);
    return () => document.removeEventListener('click', handleClick);
  }, []);

  const getFilteredSuggestions = (input) => {
    if (!input.trim()) return [];
    
    const allSuggestions = [
      // SuperClaude commands
      ...Object.entries(SUPER_CLAUDE_COMMANDS).map(([cmd, info]) => ({
        command: cmd,
        description: info.description,
        example: info.examples[0] || cmd,
        category: 'superclaude'
      })),
      // System commands
      {
        command: 'help',
        description: 'Show available commands',
        example: 'help',
        category: 'system'
      },
      {
        command: 'clear',
        description: 'Clear terminal output',
        example: 'clear',
        category: 'system'
      }
    ];
    
    return allSuggestions.filter(suggestion => 
      suggestion.command.toLowerCase().includes(input.toLowerCase()) ||
      suggestion.description.toLowerCase().includes(input.toLowerCase())
    ).slice(0, 6);
  };

  const handleInputChange = (value) => {
    setCurrentCommand(value);
    
    if (value.trim()) {
      const filtered = getFilteredSuggestions(value);
      setSuggestions(filtered);
      setShowSuggestions(filtered.length > 0);
      setSelectedSuggestion(0);
    } else {
      setShowSuggestions(false);
      setSuggestions([]);
    }
  };

  const executeSuperClaudeCommand = async (command) => {
    setIsExecuting(true);
    
    if (onSuperClaudeCommand) {
      // Use real Claude API via App.js handler
      const parts = command.split(' ');
      const baseCommand = parts[0];
      const args = parts.slice(1);
      
      try {
        // Call the real SuperClaude handler with current persona
        await onSuperClaudeCommand(baseCommand, args, selectedPersona);
      } catch (error) {
        console.error('SuperClaude command error:', error);
        onCommand(command, {
          type: 'error',
          content: `❌ Failed to execute ${baseCommand}: ${error.message}`,
          timestamp: new Date().toLocaleTimeString()
        });
      }
      setIsExecuting(false);
    } else {
      // Fallback to mock implementation if no handler provided
      const commandType = command.split(' ')[0];
      const commandInfo = SUPER_CLAUDE_COMMANDS[commandType];
      
      if (commandInfo) {
        const persona = CLAUDE_PERSONAS[selectedPersona];
        
        // Add processing message
        onCommand(`${command} --persona-${selectedPersona}`, {
          type: 'info',
          content: `🤖 ${persona.name} ${persona.emoji} analyzing... (offline mode)`,
          timestamp: new Date().toLocaleTimeString()
        });
        
        // Simulate processing delay
        setTimeout(() => {
          const responses = {
            '/analyze': `📊 Code Analysis (Offline Mode):\n\n🔍 Static Analysis Available:\n• Check your code structure and patterns\n• Review component architecture\n• Validate naming conventions\n\n💡 To enable AI-powered analysis:\n• Add your Claude API key to .env file\n• Restart the server\n• Get comprehensive insights with live AI`,
            '/build': `🏗️ Build Guidance (Offline Mode):\n\n📋 General Development Steps:\n1. Plan your component structure\n2. Set up state management\n3. Implement core functionality\n4. Add styling and responsiveness\n5. Write tests\n\n💡 Enable live AI for personalized build plans`,
            '/test': `🧪 Testing Guide (Offline Mode):\n\n✅ Testing Best Practices:\n• Unit tests for individual functions\n• Integration tests for component interactions\n• E2E tests for user workflows\n• Test edge cases and error handling\n\n💡 Enable live AI for custom test generation`,
            '/security': `🔒 Security Checklist (Offline Mode):\n\n🛡️ Basic Security Review:\n• Input validation and sanitization\n• Authentication and authorization\n• Secure API endpoints\n• Environment variable protection\n\n💡 Enable live AI for comprehensive security audits`,
            '/improve': `💡 Code Improvement (Offline Mode):\n\n🚀 General Optimization Areas:\n• Performance: Bundle size, loading times\n• Accessibility: ARIA labels, keyboard navigation\n• Code quality: DRY principles, clean functions\n• User experience: Error handling, feedback\n\n💡 Enable live AI for specific recommendations`,
            '/troubleshoot': `🔧 Troubleshooting (Offline Mode):\n\n🎯 Common Debug Steps:\n1. Check console for errors\n2. Verify network requests\n3. Review component state\n4. Test with different inputs\n5. Check dependency versions\n\n💡 Enable live AI for personalized debugging`,
            '/explain': `📚 Code Explanation (Offline Mode):\n\n💭 Understanding Code:\n• Break down complex functions\n• Trace data flow through components\n• Identify design patterns used\n• Document assumptions and logic\n\n💡 Enable live AI for detailed explanations`,
            '/design': `🎨 Design Guidance (Offline Mode):\n\n🏗️ Architecture Principles:\n• Single responsibility principle\n• Component composition\n• State management patterns\n• API design consistency\n\n💡 Enable live AI for custom architecture advice`,
            '/infinite': `🔄 Infinite Agentic Loop (Demo Mode):\n\n🚀 Starting infinite agentic loop simulation...\n\n📋 Command Format:\n• /infinite [spec_path] [output_dir] [count]\n• Default: /infinite specs/ui-spec-v3.md source_infinite infinite\n\n🤖 This will spawn multiple AI agents working in parallel:\n• Wave 1: Iterations 1-5\n• Wave 2: Iterations 6-10\n• Continue until count reached or context limits\n\n💡 Enable live AI for real infinite loops with actual code generation`
          };
          
          const response = responses[commandType] || `ℹ️ ${commandInfo.description} guidance available in offline mode.\n\n💡 Enable live AI for personalized assistance:\n1. Add Claude API key to .env file\n2. Restart the development server\n3. Get AI-powered responses`;
          
          onCommand(command, {
            type: 'info',
            content: `🔧 ${persona.name} (Offline):\n\n${response}`,
            timestamp: new Date().toLocaleTimeString()
          });
          
          setIsExecuting(false);
        }, 800);
      } else {
        onCommand(command, {
          type: 'error',
          content: `❌ Unknown SuperClaude command: ${commandType}\n\nAvailable commands: ${Object.keys(SUPER_CLAUDE_COMMANDS).join(', ')}`,
          timestamp: new Date().toLocaleTimeString()
        });
        setIsExecuting(false);
      }
    }
  };

  const handleSubmit = (e) => {
    if (e) e.preventDefault();
    if (currentCommand.trim()) {
      // Add to history
      const newHistory = [...commandHistory, currentCommand];
      setCommandHistory(newHistory);
      setHistoryIndex(-1);
      
      // Check if it's a SuperClaude command
      if (currentCommand.startsWith('/ui ')) {
        // Handle React Bits commands
        onCommand(currentCommand);
      } else if (currentCommand.startsWith('/')) {
        executeSuperClaudeCommand(currentCommand);
      } else if (currentCommand.toLowerCase().trim() === 'claude') {
        // Handle native Claude command
        onCommand('claude', {
          type: 'info',
          content: '🤖 Claude Code Assistant activated!\n\nHello! I\'m Claude, your AI coding assistant. How can I help you today?\n\n• Ask questions about your code\n• Get explanations and guidance\n• Request help with debugging\n• Discuss best practices\n\nType naturally, just like you would in the real Claude Code experience.',
          timestamp: new Date().toLocaleTimeString()
        });
      } else {
        // Execute regular command
        onCommand(currentCommand);
      }
      
      setCurrentCommand('');
      setShowSuggestions(false);
    }
  };

  const handleKeyDown = (e) => {
    if (showSuggestions && suggestions.length > 0) {
      if (e.key === 'Tab' || e.key === 'Enter') {
        e.preventDefault();
        const selectedCmd = suggestions[selectedSuggestion];
        setCurrentCommand(selectedCmd.command);
        setShowSuggestions(false);
        inputRef.current?.focus();
        return;
      }
      
      if (e.key === 'ArrowDown') {
        e.preventDefault();
        setSelectedSuggestion(prev => Math.min(suggestions.length - 1, prev + 1));
        return;
      }
      
      if (e.key === 'ArrowUp') {
        e.preventDefault();
        setSelectedSuggestion(prev => Math.max(0, prev - 1));
        return;
      }
      
      if (e.key === 'Escape') {
        setShowSuggestions(false);
        return;
      }
    }
    
    // Original arrow key history navigation
    if (e.key === 'ArrowUp' && !showSuggestions) {
      e.preventDefault();
      if (commandHistory.length > 0) {
        const newIndex = historyIndex === -1 ? commandHistory.length - 1 : Math.max(0, historyIndex - 1);
        setHistoryIndex(newIndex);
        setCurrentCommand(commandHistory[newIndex]);
      }
    } else if (e.key === 'ArrowDown' && !showSuggestions) {
      e.preventDefault();
      if (historyIndex >= 0) {
        const newIndex = historyIndex + 1;
        if (newIndex >= commandHistory.length) {
          setHistoryIndex(-1);
          setCurrentCommand('');
        } else {
          setHistoryIndex(newIndex);
          setCurrentCommand(commandHistory[newIndex]);
        }
      }
    }
  };

  const getOutputClass = (entry) => {
    switch (entry.type) {
      case 'command':
        return 'command-line';
      case 'success':
        return 'success-line';
      case 'error':
        return 'error-line';
      case 'info':
        return 'info-line';
      default:
        return 'output-line';
    }
  };

  const formatOutput = (content) => {
    // Handle ANSI color codes and special formatting
    return content.replace(/\n/g, '<br>');
  };

  const clearTerminal = () => {
    onCommand('clear');
  };

  const selectPersona = (personaId) => {
    setSelectedPersona(personaId);
    setShowPersonaSelector(false);
  };

  return (
    <div className={`terminal ${isCollapsed ? 'collapsed' : ''}`} ref={containerRef}>
      <div className="terminal-header">
        <div className="terminal-header-left">
          {/* Removed title to save space */}
        </div>
        <div className="terminal-header-right">
          <div className="terminal-controls">
            <button 
              className={`terminal-control-btn ${supervisionEnabled ? 'supervision-active' : ''} ${currentPage === 'supervision' ? 'active' : ''}`}
              onClick={() => setCurrentPage && setCurrentPage(currentPage === 'supervision' ? 'ide' : 'supervision')}
              title="Toggle Supervision Engine"
            >
              <span className="control-icon">👁️</span>
              <span className="control-label">Supervision</span>
            </button>
            <button 
              className={`terminal-control-btn ${currentPage === 'sleep-mode' ? 'active' : ''}`}
              onClick={() => setCurrentPage && setCurrentPage(currentPage === 'sleep-mode' ? 'ide' : 'sleep-mode')}
              title="Toggle Sleep Mode (Infinity Mode)"
            >
              <span className="control-icon">🌙</span>
              <span className="control-label">Sleep Mode</span>
            </button>
            <button 
              className={`terminal-control-btn ${currentPage === 'infinite-loop' ? 'active' : ''}`}
              onClick={() => setCurrentPage && setCurrentPage(currentPage === 'infinite-loop' ? 'ide' : 'infinite-loop')}
              title="Infinite Agentic Loop - Generate unlimited solutions"
            >
              <span className="control-icon">🔄</span>
              <span className="control-label">Infinite Loop</span>
            </button>
            <button 
              className={`terminal-control-btn ${currentPage === 'parallel-agents' ? 'active' : ''}`}
              onClick={() => setCurrentPage && setCurrentPage(currentPage === 'parallel-agents' ? 'ide' : 'parallel-agents')}
              title="Parallel Agentic Coding - Run multiple agents in parallel"
            >
              <span className="control-icon">⚡</span>
              <span className="control-label">Parallel Agents</span>
            </button>
          </div>
          <div className="terminal-header-actions">
            {selectedPersona && (
              <div className="persona-indicator-container">
                <span className="persona-indicator">
                  {CLAUDE_PERSONAS[selectedPersona].name}
                </span>
              </div>
            )}
            <button 
              className="terminal-btn persona-btn"
              onClick={() => setShowPersonaSelector(!showPersonaSelector)}
              title="Select AI Persona"
            >
              👤
            </button>
            <button 
              className="terminal-btn"
              onClick={clearTerminal}
              title="Clear Terminal"
            >
              🗑️
            </button>
          </div>
        </div>
      </div>
      
      {showPersonaSelector && (
        <div className="persona-selector">
          <div className="persona-grid">
            {Object.entries(CLAUDE_PERSONAS).map(([id, persona]) => (
              <button
                key={id}
                className={`persona-card ${selectedPersona === id ? 'selected' : ''}`}
                onClick={() => selectPersona(id)}
              >
                <span className="persona-emoji">{persona.emoji}</span>
                <span className="persona-name">{persona.name}</span>
                <span className="persona-desc">{persona.description}</span>
              </button>
            ))}
          </div>
        </div>
      )}
      
      {!isCollapsed && (
        <>
          <div className="terminal-content" ref={terminalRef}>
            {output.map((entry, index) => (
              <div key={index} className={`terminal-line ${getOutputClass(entry)}`}>
                {entry.type === 'command' && <span className="prompt">$ </span>}
                <span 
                  className="line-content"
                  dangerouslySetInnerHTML={{ __html: formatOutput(entry.content) }}
                />
                <span className="timestamp">{entry.timestamp}</span>
              </div>
            ))}
            {output.length === 0 && (
              <div className="terminal-line info-line">
                <span className="line-content">
                  🤖 SuperClaude Terminal ready. Type '/' for AI commands or 'help' for more options.
                </span>
              </div>
            )}
            {isExecuting && (
              <div className="terminal-line info-line executing">
                <span className="line-content">
                  ⚡ Processing command... {CLAUDE_PERSONAS[selectedPersona].emoji}
                </span>
              </div>
            )}
            
            {/* Current command line - inline terminal input */}
            <div className="terminal-line current-command-line">
              <span className="prompt">$ </span>
              <span className="line-content">
                {currentCommand}
                <span className={`cursor ${isFocused ? 'blink' : ''}`}>█</span>
              </span>
            </div>
          </div>
          
          {/* Command suggestions positioned above current command */}
          {showSuggestions && suggestions.length > 0 && (
            <div className="suggestions-overlay">
              <div className="suggestions-dropdown">
                {suggestions.map((suggestion, index) => (
                  <div 
                    key={suggestion.command}
                    className={`suggestion-item ${index === selectedSuggestion ? 'selected' : ''}`}
                    onClick={() => {
                      setCurrentCommand(suggestion.command);
                      setShowSuggestions(false);
                    }}
                  >
                    <div className="suggestion-command">
                      <span className={`suggestion-icon ${suggestion.category}`}>
                        {suggestion.category === 'superclaude' ? '🤖' : '⚡'}
                      </span>
                      <span className="command-text">{suggestion.command}</span>
                    </div>
                    <div className="suggestion-description">{suggestion.description}</div>
                    {index === selectedSuggestion && (
                      <div className="suggestion-hint">Tab or Enter to select</div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default TerminalEnhanced;