import React, { useState, useEffect } from 'react';
import './ParallelAgentPage.css';

const ParallelAgentPage = ({ setCurrentPage }) => {
  const [currentStep, setCurrentStep] = useState('setup'); // setup, executing, results
  const [availablePlans, setAvailablePlans] = useState([]);
  const [selectedPlan, setSelectedPlan] = useState('');
  const [customPlan, setCustomPlan] = useState('');
  const [config, setConfig] = useState({
    featureName: '',
    numAgents: 3,
    planType: 'template' // template or custom
  });

  const [session, setSession] = useState(null);
  const [logs, setLogs] = useState([]);
  const [results, setResults] = useState(null);

  useEffect(() => {
    loadAvailablePlans();
  }, []);

  const addLog = (message, type = 'info') => {
    const timestamp = new Date().toLocaleTimeString();
    setLogs(prev => [...prev, { message, type, timestamp }]);
  };

  const loadAvailablePlans = async () => {
    try {
      const response = await fetch('http://localhost:3002/api/parallel/plans');
      const data = await response.json();
      
      if (data.success) {
        setAvailablePlans(data.plans);
        if (data.plans.length > 0) {
          setSelectedPlan(data.plans[0].name);
        }
      }
    } catch (error) {
      console.error('Failed to load plans:', error);
      addLog('Failed to load available plans', 'error');
    }
  };

  const handleConfigChange = (key, value) => {
    setConfig(prev => ({ ...prev, [key]: value }));
  };

  const testConnection = async () => {
    try {
      addLog('🔗 Testing parallel system connection...', 'info');
      const response = await fetch('http://localhost:3002/api/parallel/test-connection');
      const data = await response.json();
      
      if (data.success) {
        addLog('✅ Parallel system connection successful', 'success');
        return true;
      } else {
        addLog(`❌ Connection failed: ${data.error}`, 'error');
        return false;
      }
    } catch (error) {
      addLog(`❌ Connection test failed: ${error.message}`, 'error');
      return false;
    }
  };

  const initializeWorkspace = async () => {
    if (!config.featureName.trim()) {
      addLog('❌ Feature name is required', 'error');
      return false;
    }

    try {
      addLog('🚀 Initializing parallel workspace...', 'info');
      addLog(`📋 Feature: ${config.featureName}`, 'info');
      addLog(`🤖 Agents: ${config.numAgents}`, 'info');

      const response = await fetch('http://localhost:3002/api/parallel/init', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          featureName: config.featureName,
          numAgents: config.numAgents
        })
      });

      const data = await response.json();
      
      if (data.success) {
        setSession(data.session);
        addLog(`✅ Workspace initialized: ${data.sessionId}`, 'success');
        addLog(`🌳 Created ${data.session.workTrees.length} git work trees`, 'info');
        
        // Log work tree status
        data.session.workTrees.forEach((tree, index) => {
          if (tree.status === 'ready') {
            addLog(`✅ Agent ${index + 1}: ${tree.name} ready`, 'success');
          } else {
            addLog(`❌ Agent ${index + 1}: ${tree.name} failed - ${tree.error}`, 'error');
          }
        });
        
        return true;
      } else {
        addLog(`❌ Failed to initialize workspace: ${data.error}`, 'error');
        return false;
      }
    } catch (error) {
      addLog(`❌ Initialization failed: ${error.message}`, 'error');
      return false;
    }
  };

  const executeParallelPlan = async () => {
    if (!session) {
      addLog('❌ No active session', 'error');
      return;
    }

    try {
      addLog('🌊 Starting parallel plan execution...', 'info');
      addLog(`📝 Plan type: ${config.planType}`, 'info');
      
      const requestBody = {
        planPath: config.planType === 'template' ? `plans/${selectedPlan}.md` : undefined,
        customPlan: config.planType === 'custom' ? customPlan : undefined
      };

      const response = await fetch(`http://localhost:3002/api/parallel/execute/${session.id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(requestBody)
      });

      const data = await response.json();
      
      if (data.success) {
        setSession(data.session);
        addLog('🎉 Parallel execution completed!', 'success');
        
        // Log results for each agent
        data.session.results.forEach((result, index) => {
          if (result.success) {
            addLog(`✅ Agent ${result.agentId}: Completed successfully`, 'success');
            addLog(`📁 Agent ${result.agentId}: ${result.implementation?.fileChanges?.length || 0} files changed`, 'info');
          } else {
            addLog(`❌ Agent ${result.agentId}: Failed - ${result.error}`, 'error');
          }
        });

        // Load detailed results
        await loadResults();
        setCurrentStep('results');
      } else {
        addLog(`❌ Execution failed: ${data.error}`, 'error');
      }
    } catch (error) {
      addLog(`❌ Execution error: ${error.message}`, 'error');
    }
  };

  const loadResults = async () => {
    if (!session) return;

    try {
      const response = await fetch(`http://localhost:3002/api/parallel/results/${session.id}`);
      const data = await response.json();
      
      if (data.success) {
        setResults(data.comparison);
      }
    } catch (error) {
      console.error('Failed to load results:', error);
    }
  };

  const startParallelExecution = async () => {
    setCurrentStep('executing');
    setLogs([]);

    // Test connection first
    const connectionOk = await testConnection();
    if (!connectionOk) {
      setCurrentStep('setup');
      return;
    }

    // Initialize workspace
    const workspaceOk = await initializeWorkspace();
    if (!workspaceOk) {
      setCurrentStep('setup');
      return;
    }

    // Execute plan
    await executeParallelPlan();
  };

  const resetSession = () => {
    setCurrentStep('setup');
    setSession(null);
    setResults(null);
    setLogs([]);
  };

  const cleanupSession = async () => {
    if (!session) return;

    try {
      addLog('🧹 Cleaning up parallel session...', 'info');
      const response = await fetch(`http://localhost:3002/api/parallel/cleanup/${session.id}`, {
        method: 'DELETE'
      });

      const data = await response.json();
      if (data.success) {
        addLog('✅ Session cleaned up successfully', 'success');
        resetSession();
      } else {
        addLog(`❌ Cleanup failed: ${data.error}`, 'error');
      }
    } catch (error) {
      addLog(`❌ Cleanup error: ${error.message}`, 'error');
    }
  };

  return (
    <div className="parallel-agent-page">
      <div className="page-header">
        <button 
          className="back-to-ide-btn"
          onClick={() => setCurrentPage('ide')}
          title="Back to IDE"
        >
          ← Back to IDE
        </button>
        <h1>⚡ Parallel Agentic Coding</h1>
        <p>Run multiple Claude agents in parallel to generate different approaches to the same problem</p>
      </div>

      <div className="parallel-content">
        {currentStep === 'setup' && (
          <div className="setup-panel">
            <h2>🚀 Setup Parallel Execution</h2>
            
            <div className="config-section">
              <h3>Feature Configuration</h3>
              <div className="config-row">
                <label>Feature Name:</label>
                <input
                  type="text"
                  value={config.featureName}
                  onChange={(e) => handleConfigChange('featureName', e.target.value)}
                  placeholder="e.g., ui-revamp, new-dashboard, auth-system"
                />
              </div>
              <div className="config-row">
                <label>Number of Agents:</label>
                <select
                  value={config.numAgents}
                  onChange={(e) => handleConfigChange('numAgents', parseInt(e.target.value))}
                >
                  <option value={2}>2 Agents</option>
                  <option value={3}>3 Agents</option>
                  <option value={4}>4 Agents</option>
                  <option value={5}>5 Agents</option>
                </select>
              </div>
            </div>

            <div className="config-section">
              <h3>Plan Selection</h3>
              <div className="plan-type-selector">
                <label>
                  <input
                    type="radio"
                    value="template"
                    checked={config.planType === 'template'}
                    onChange={(e) => handleConfigChange('planType', e.target.value)}
                  />
                  Use Template Plan
                </label>
                <label>
                  <input
                    type="radio"
                    value="custom"
                    checked={config.planType === 'custom'}
                    onChange={(e) => handleConfigChange('planType', e.target.value)}
                  />
                  Custom Plan
                </label>
              </div>

              {config.planType === 'template' && (
                <div className="template-selector">
                  <label>Select Template:</label>
                  <select
                    value={selectedPlan}
                    onChange={(e) => setSelectedPlan(e.target.value)}
                  >
                    {availablePlans.map(plan => (
                      <option key={plan.name} value={plan.name}>
                        {plan.title}
                      </option>
                    ))}
                  </select>
                </div>
              )}

              {config.planType === 'custom' && (
                <div className="custom-plan">
                  <label>Custom Plan:</label>
                  <textarea
                    value={customPlan}
                    onChange={(e) => setCustomPlan(e.target.value)}
                    placeholder="Write your detailed development plan here..."
                    rows={12}
                  />
                </div>
              )}
            </div>

            <div className="control-buttons">
              <button 
                className="start-btn" 
                onClick={startParallelExecution}
                disabled={!config.featureName.trim() || (config.planType === 'custom' && !customPlan.trim())}
              >
                🚀 Start Parallel Execution
              </button>
            </div>
          </div>
        )}

        {currentStep === 'executing' && (
          <div className="execution-panel">
            <h2>⚡ Parallel Execution in Progress</h2>
            
            <div className="status-display">
              {session && (
                <div className="session-info">
                  <div className="status-item">
                    <span className="status-label">Session ID:</span>
                    <span className="status-value session-id">{session.id}</span>
                  </div>
                  <div className="status-item">
                    <span className="status-label">Feature:</span>
                    <span className="status-value">{session.featureName}</span>
                  </div>
                  <div className="status-item">
                    <span className="status-label">Agents:</span>
                    <span className="status-value">{session.numAgents}</span>
                  </div>
                  <div className="status-item">
                    <span className="status-label">Status:</span>
                    <span className={`status-value ${session.status}`}>{session.status}</span>
                  </div>
                </div>
              )}
            </div>

            <div className="logs-container">
              <div className="logs-header">
                <h3>Execution Logs</h3>
                <span className="log-count">{logs.length} entries</span>
              </div>
              <div className="logs-display">
                {logs.map((log, index) => (
                  <div key={index} className={`log-entry ${log.type}`}>
                    <span className="log-timestamp">{log.timestamp}</span>
                    <span className="log-message">{log.message}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {currentStep === 'results' && results && (
          <div className="results-panel">
            <h2>📊 Parallel Execution Results</h2>
            
            <div className="results-summary">
              <div className="summary-card">
                <h3>Execution Summary</h3>
                <div className="summary-stats">
                  <div className="stat">
                    <span className="stat-label">Total Agents:</span>
                    <span className="stat-value">{results.totalAgents}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">Successful:</span>
                    <span className="stat-value success">{results.successfulAgents}</span>
                  </div>
                  <div className="stat">
                    <span className="stat-label">Failed:</span>
                    <span className="stat-value error">{results.failedAgents}</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="agents-comparison">
              <h3>Agent Results Comparison</h3>
              <div className="agents-grid">
                {results.agents.map((agent, index) => (
                  <div key={index} className={`agent-card ${agent.success ? 'success' : 'failed'}`}>
                    <div className="agent-header">
                      <h4>Agent {agent.agentId}</h4>
                      <span className={`agent-status ${agent.success ? 'success' : 'failed'}`}>
                        {agent.success ? '✅ Success' : '❌ Failed'}
                      </span>
                    </div>
                    <div className="agent-details">
                      <div className="agent-detail">
                        <span className="detail-label">Work Tree:</span>
                        <span className="detail-value">{agent.workTree}</span>
                      </div>
                      <div className="agent-detail">
                        <span className="detail-label">Branch:</span>
                        <span className="detail-value">{agent.branch}</span>
                      </div>
                      {agent.success && (
                        <>
                          <div className="agent-detail">
                            <span className="detail-label">Files Changed:</span>
                            <span className="detail-value">{agent.fileChanges}</span>
                          </div>
                          <div className="agent-detail">
                            <span className="detail-label">Code Blocks:</span>
                            <span className="detail-value">{agent.codeBlocks}</span>
                          </div>
                        </>
                      )}
                      <div className="agent-summary">
                        <span className="detail-label">Summary:</span>
                        <p className="detail-value">{agent.summary}</p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            <div className="recommendations">
              <h3>🎯 Recommendations</h3>
              <ul>
                {results.recommendations.map((rec, index) => (
                  <li key={index}>{rec}</li>
                ))}
              </ul>
            </div>

            <div className="control-buttons">
              <button className="cleanup-btn" onClick={cleanupSession}>
                🧹 Cleanup Session
              </button>
              <button className="new-btn" onClick={resetSession}>
                🚀 New Parallel Execution
              </button>
            </div>
          </div>
        )}
      </div>

      <div className="info-footer">
        <div className="info-card">
          <h3>⚡ How Parallel Agentic Coding Works</h3>
          <ul>
            <li>Creates isolated git work trees for each agent</li>
            <li>Multiple Claude agents work on the same plan simultaneously</li>
            <li>Each agent develops a unique approach to the problem</li>
            <li>Compare results and merge the best implementation</li>
            <li>Leverage non-deterministic nature of LLMs for creativity</li>
          </ul>
        </div>
        <div className="info-card">
          <h3>🎯 Best Practices</h3>
          <ul>
            <li>Write detailed, clear plans for better results</li>
            <li>Use 3-5 agents for optimal variety vs. cost</li>
            <li>Review all successful agents before choosing</li>
            <li>Clean up sessions to avoid git branch bloat</li>
            <li>Test on smaller features before large implementations</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default ParallelAgentPage;