import { useState, useEffect, useCallback } from 'react';
import { getSupervisionService } from './SupervisionService';

// Custom hook for supervision state management
export const useSupervision = () => {
  const [state, setState] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const supervisionService = getSupervisionService();

  // Initialize state and subscribe to changes
  useEffect(() => {
    setState(supervisionService.getState());
    
    const unsubscribe = supervisionService.subscribe((newState) => {
      setState(newState);
    });

    return unsubscribe;
  }, [supervisionService]);

  // Enable supervision
  const enableSupervision = useCallback(async (config = {}) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = supervisionService.enableSupervision(config);
      if (!result) {
        throw new Error('Failed to enable supervision');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Disable supervision
  const disableSupervision = useCallback(async () => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = supervisionService.disableSupervision();
      if (!result) {
        throw new Error('Failed to disable supervision');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Add supervisor
  const addSupervisor = useCallback(async (personaId) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = supervisionService.addSupervisor(personaId);
      if (!result) {
        throw new Error('Failed to add supervisor');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Remove supervisor
  const removeSupervisor = useCallback(async (personaId) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = supervisionService.removeSupervisor(personaId);
      if (!result) {
        throw new Error('Failed to remove supervisor');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Set autonomy level
  const setAutonomyLevel = useCallback(async (level) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = supervisionService.setAutonomyLevel(level);
      if (!result) {
        throw new Error('Failed to set autonomy level');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Request decision
  const requestDecision = useCallback(async (context) => {
    setIsLoading(true);
    setError(null);
    
    try {
      const result = await supervisionService.requestDecision(context);
      return result;
    } catch (err) {
      setError(err.message);
      return { approved: false, reason: err.message };
    } finally {
      setIsLoading(false);
    }
  }, [supervisionService]);

  // Update quality gate
  const updateQualityGate = useCallback(async (gateName, score) => {
    setError(null);
    
    try {
      const result = supervisionService.updateQualityGate(gateName, score);
      if (!result) {
        throw new Error('Failed to update quality gate');
      }
    } catch (err) {
      setError(err.message);
    }
  }, [supervisionService]);

  // Get metrics
  const getMetrics = useCallback(() => {
    return supervisionService.getSupervisionMetrics();
  }, [supervisionService]);

  // Reset supervision
  const resetSupervision = useCallback(() => {
    supervisionService.reset();
  }, [supervisionService]);

  return {
    // State
    state,
    isLoading,
    error,
    
    // Actions
    enableSupervision,
    disableSupervision,
    addSupervisor,
    removeSupervisor,
    setAutonomyLevel,
    requestDecision,
    updateQualityGate,
    getMetrics,
    resetSupervision,
    
    // Computed properties
    isEnabled: state?.isEnabled || false,
    supervisors: state?.supervisors || [],
    decisions: state?.decisions || [],
    autonomyLevel: state?.autonomyLevel || 'balanced',
    qualityGates: state?.qualityGates || {},
    
    // Service instance for advanced usage
    service: supervisionService
  };
};

export default useSupervision;