/**
 * Claude API Service for SuperClaude Integration
 * Handles real Claude API calls with proper authentication and error handling
 */

class ClaudeAPI {
  constructor() {
    this.apiKey = process.env.REACT_APP_CLAUDE_API_KEY;
    this.apiUrl = process.env.REACT_APP_CLAUDE_API_URL || 'https://api.anthropic.com/v1';
    this.model = 'claude-3-5-sonnet-20241022';
    
    if (!this.apiKey || this.apiKey === 'your-claude-api-key-here') {
      console.warn('Claude API key not configured. SuperClaude will run in offline mode.');
      console.warn('To enable live AI features:');
      console.warn('1. Get your Claude API key from https://console.anthropic.com/');
      console.warn('2. Update REACT_APP_CLAUDE_API_KEY in the .env file');
      console.warn('3. Restart the development server');
    }
  }

  /**
   * Send message to Claude API
   */
  async sendMessage(messages, systemPrompt = '', maxTokens = 4096, userApiKey = null) {
    // Use user-provided API key (from localStorage) or fallback to env var
    const apiKey = userApiKey || this.getUserApiKey() || this.apiKey;
    
    if (!apiKey || apiKey === 'your-claude-api-key-here') {
      throw new Error('Claude API key not configured. Please add your API key in IDE settings.');
    }

    try {
      // Use backend endpoint to handle CORS
      const backendUrl = process.env.NODE_ENV === 'development' 
        ? 'http://localhost:3002/api/claude'
        : '/api/claude';
        
      const response = await fetch(backendUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          messages: messages,
          systemPrompt: systemPrompt,
          maxTokens: maxTokens,
          apiKey: apiKey
        })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(`API error: ${response.status} - ${errorData.error || 'Unknown error'}`);
      }

      const data = await response.json();
      if (data.success) {
        return data.response;
      } else {
        throw new Error(data.error || 'Unknown error from Claude API');
      }
    } catch (error) {
      console.error('Claude API request failed:', error);
      if (error.message.includes('Failed to fetch')) {
        throw new Error('Cannot connect to backend server. Please ensure the backend is running on port 3002.');
      }
      throw error;
    }
  }

  /**
   * Get user API key from localStorage
   */
  getUserApiKey() {
    if (typeof window !== 'undefined') {
      return localStorage.getItem('claudeApiKey');
    }
    return null;
  }

  /**
   * Set user API key in localStorage
   */
  setUserApiKey(apiKey) {
    if (typeof window !== 'undefined') {
      localStorage.setItem('claudeApiKey', apiKey);
    }
  }

  /**
   * Check if user has valid API key configured
   */
  hasValidApiKey() {
    const apiKey = this.getUserApiKey();
    return apiKey && apiKey !== 'your-claude-api-key-here' && apiKey.startsWith('sk-ant-');
  }

  /**
   * Execute SuperClaude command with proper prompting
   */
  async executeCommand(command, args, persona, context = {}) {
    const { activeFile, openFiles, projectStructure } = context;
    
    // Build system prompt based on SuperClaude specifications
    const systemPrompt = this.buildSystemPrompt(command, persona, context);
    
    // Build user message with context
    const userMessage = this.buildUserMessage(command, args, context);
    
    try {
      const response = await this.sendMessage([{
        role: 'user',
        content: userMessage
      }], systemPrompt);
      
      return {
        success: true,
        response: response,
        command: command,
        persona: persona
      };
    } catch (error) {
      return {
        success: false,
        error: error.message,
        command: command,
        persona: persona
      };
    }
  }

  /**
   * Build system prompt based on SuperClaude specifications
   */
  buildSystemPrompt(command, persona, context) {
    const basePrompt = `You are SuperClaude, an advanced AI coding assistant with specialized expertise. You follow evidence-based development practices and provide precise, actionable guidance.

CORE PRINCIPLES:
- Use evidence-based language: "may", "could", "potentially", "measured", "documented"
- Avoid absolute terms: "best", "optimal", "always", "never", "guaranteed"
- Provide specific, actionable recommendations
- Focus on code quality, security, and maintainability
- Include relevant examples and explanations`;

    const personaPrompts = {
      frontend: `PERSONA: Frontend Expert - UI/UX specialist with React/Vue expertise
- Focus on component architecture, accessibility, and user experience
- Consider responsive design, performance, and modern frontend patterns
- Emphasize accessibility standards and inclusive design`,

      backend: `PERSONA: Backend Expert - API design and scalability specialist  
- Focus on server architecture, database design, and API patterns
- Consider scalability, security, and reliability engineering
- Emphasize clean architecture and maintainable code`,

      security: `PERSONA: Security Expert - Cybersecurity and compliance specialist
- Focus on threat modeling, vulnerability assessment, and secure coding
- Consider OWASP guidelines, encryption, and access control
- Emphasize security best practices and compliance requirements`,

      qa: `PERSONA: QA Expert - Testing and quality assurance specialist
- Focus on test strategy, coverage analysis, and quality metrics
- Consider unit, integration, and end-to-end testing approaches
- Emphasize edge cases, error handling, and validation`,

      performance: `PERSONA: Performance Expert - Optimization and profiling specialist
- Focus on performance metrics, bottleneck identification, and optimization
- Consider memory usage, CPU efficiency, and scalability
- Emphasize measurement-driven optimization strategies`,

      architect: `PERSONA: Solution Architect - System design and architecture specialist
- Focus on high-level design, technology decisions, and scalability
- Consider long-term maintainability and system evolution
- Emphasize architectural patterns and design principles`,

      analyzer: `PERSONA: Code Analyzer - Investigation and debugging specialist
- Focus on root cause analysis and evidence-based investigation
- Consider code quality metrics and technical debt assessment
- Emphasize systematic problem-solving approaches`,

      refactorer: `PERSONA: Refactoring Expert - Code quality and maintainability specialist
- Focus on code cleanup, technical debt reduction, and structure improvement
- Consider readability, maintainability, and design patterns
- Emphasize incremental improvement strategies`,

      mentor: `PERSONA: Code Mentor - Education and knowledge transfer specialist
- Focus on clear explanations, learning paths, and best practices
- Consider different skill levels and learning approaches
- Emphasize practical examples and hands-on guidance`
    };

    const commandPrompts = {
      '/analyze': 'Perform comprehensive code analysis. Examine code quality, architecture, security, and performance. Provide structured findings with specific recommendations.',
      '/build': 'Assist with feature development and implementation. Provide step-by-step guidance, code examples, and architectural considerations.',
      '/test': 'Generate comprehensive testing strategies and test code. Focus on coverage, edge cases, and quality assurance.',
      '/security': 'Conduct security audit and vulnerability assessment. Check for OWASP top 10, security best practices, and compliance issues.',
      '/improve': 'Identify optimization opportunities and provide enhancement recommendations. Focus on performance, maintainability, and code quality.',
      '/troubleshoot': 'Investigate issues and provide debugging guidance. Use systematic problem-solving approaches.',
      '/explain': 'Provide clear explanations of code concepts, patterns, and implementations. Focus on educational value.',
      '/design': 'Help with system architecture and design decisions. Consider scalability, maintainability, and best practices.'
    };

    return `${basePrompt}

${personaPrompts[persona] || ''}

COMMAND CONTEXT: ${commandPrompts[command] || 'General assistance'}

RESPONSE FORMAT:
- Start with a brief summary
- Provide specific, actionable recommendations
- Include code examples when relevant
- End with next steps or follow-up actions`;
  }

  /**
   * Build user message with context information
   */
  buildUserMessage(command, args, context) {
    const { activeFile, openFiles, projectStructure } = context;
    
    let message = `Command: ${command}`;
    
    if (args && args.length > 0) {
      message += `\nArguments: ${args.join(' ')}`;
    }
    
    if (activeFile) {
      message += `\n\nActive File: ${activeFile.name}`;
      if (activeFile.content && activeFile.content.length < 10000) {
        message += `\nFile Content:\n\`\`\`\n${activeFile.content}\n\`\`\``;
      }
    }
    
    if (openFiles && openFiles.length > 1) {
      message += `\n\nOpen Files: ${openFiles.map(f => f.name).join(', ')}`;
    }
    
    if (projectStructure) {
      message += `\n\nProject Context: ${JSON.stringify(projectStructure, null, 2)}`;
    }
    
    return message;
  }

  /**
   * Get available personas
   */
  getPersonas() {
    return [
      { id: 'frontend', name: 'Frontend Expert', emoji: '🎨', description: 'UI/UX and React specialist' },
      { id: 'backend', name: 'Backend Expert', emoji: '⚙️', description: 'API and server architecture' },
      { id: 'security', name: 'Security Expert', emoji: '🔒', description: 'Security and compliance' },
      { id: 'qa', name: 'QA Expert', emoji: '🧪', description: 'Testing and quality' },
      { id: 'performance', name: 'Performance Expert', emoji: '⚡', description: 'Optimization specialist' },
      { id: 'architect', name: 'Solution Architect', emoji: '🏗️', description: 'System design' },
      { id: 'analyzer', name: 'Code Analyzer', emoji: '🔍', description: 'Investigation specialist' },
      { id: 'refactorer', name: 'Refactoring Expert', emoji: '🔧', description: 'Code improvement' },
      { id: 'mentor', name: 'Code Mentor', emoji: '👨‍🏫', description: 'Education and guidance' }
    ];
  }

  /**
   * Get available commands with their specifications
   */
  getCommands() {
    return {
      '/analyze': {
        name: '/analyze',
        description: 'Comprehensive code analysis with AI insights',
        flags: ['--code', '--arch', '--security', '--performance', '--fs', '--git', '--fc'],
        examples: ['/analyze --code', '/analyze --security --performance']
      },
      '/build': {
        name: '/build',
        description: 'AI-assisted feature development and implementation',
        flags: ['--init', '--feature', '--react', '--api', '--tdd', '--fc'],
        examples: ['/build --feature user-dashboard', '/build --react --tdd']
      },
      '/test': {
        name: '/test',
        description: 'Generate comprehensive tests and quality assurance',
        flags: ['--coverage', '--e2e', '--bu', '--validate', '--unit'],
        examples: ['/test --coverage', '/test --e2e --bu']
      },
      '/security': {
        name: '/security',
        description: 'Security audit and vulnerability assessment',
        flags: ['--owasp', '--deps', '--validate', '--fs', '--scan'],
        examples: ['/security --owasp', '/security --scan --deps']
      },
      '/improve': {
        name: '/improve',
        description: 'Code optimization and enhancement suggestions',
        flags: ['--quality', '--performance', '--security', '--iterate', '--fs'],
        examples: ['/improve --performance', '/improve --quality --fs']
      },
      '/troubleshoot': {
        name: '/troubleshoot',
        description: 'Debug and solve development issues',
        flags: ['--investigate', '--evidence', '--all-mcp', '--debug'],
        examples: ['/troubleshoot --debug', '/troubleshoot --all-mcp']
      },
      '/explain': {
        name: '/explain',
        description: 'Explain code functionality and concepts',
        flags: ['--detailed', '--beginner', '--examples', '--patterns'],
        examples: ['/explain React hooks', '/explain --detailed --examples']
      },
      '/design': {
        name: '/design',
        description: 'System architecture and design planning',
        flags: ['--api', '--ddd', '--microservices', '--ultrathink', '--fc'],
        examples: ['/design user-auth-system', '/design --api --microservices']
      }
    };
  }
}

export default new ClaudeAPI();