import React, { useState, useEffect } from 'react';
import { useSupervision } from './useSupervision';
import './SleepModeManager.css';

const SleepModeManager = ({ 
  workspaceId = 'default', 
  onStatusChange,
  status,
  config,
  onToggleSleepMode,
  onConfigChange
}) => {
  const { isEnabled: supervisionEnabled, enableSupervision, disableSupervision } = useSupervision();

  const [lastActivity, setLastActivity] = useState(null);

  // Update last activity when status changes
  useEffect(() => {
    if (status?.isActive) {
      setLastActivity(new Date());
    }
  }, [status?.changesApproved]);

  const handleConfigChange = (key, value) => {
    if (onConfigChange) {
      onConfigChange(key, value);
    }
  };

  const handleThresholdChange = (key, value) => {
    if (onConfigChange) {
      onConfigChange('threshold', { key, value });
    }
  };

  const getUptime = () => {
    if (!status?.startTime) return null;
    const now = new Date();
    const diff = now - status.startTime;
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    return `${hours}h ${minutes}m`;
  };

  const getEfficiencyRate = () => {
    if (!status) return 0;
    const total = status.changesApproved + status.changesRejected;
    if (total === 0) return 100;
    return Math.round((status.changesApproved / total) * 100);
  };

  return (
    <div className="sleep-mode-manager">
      <div className="sleep-mode-header">
        <div className="header-content">
          <h3>🌙 Sleep Mode (Infinity Mode)</h3>
          <p>Autonomous overnight development with AI supervision</p>
        </div>
        <div className={`status-indicator ${status?.isActive ? 'active' : 'inactive'}`}>
          {status?.isActive ? 'ACTIVE' : 'INACTIVE'}
        </div>
      </div>

      {status?.isActive && (
        <div className="sleep-mode-stats">
          <div className="stats-grid">
            <div className="stat-item">
              <span className="stat-label">Changes Approved</span>
              <span className="stat-value approved">{status?.changesApproved || 0}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Changes Rejected</span>
              <span className="stat-value rejected">{status?.changesRejected || 0}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Escalations</span>
              <span className="stat-value escalations">{status?.escalationsTriggered || 0}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Efficiency Rate</span>
              <span className="stat-value efficiency">{getEfficiencyRate()}%</span>
            </div>
          </div>
          
          {status?.startTime && (
            <div className="runtime-info">
              <div className="runtime-item">
                <span className="runtime-label">Active Since:</span>
                <span className="runtime-value">{status.startTime.toLocaleString()}</span>
              </div>
              <div className="runtime-item">
                <span className="runtime-label">Uptime:</span>
                <span className="runtime-value">{getUptime()}</span>
              </div>
              {lastActivity && (
                <div className="runtime-item">
                  <span className="runtime-label">Last Activity:</span>
                  <span className="runtime-value">{lastActivity.toLocaleTimeString()}</span>
                </div>
              )}
            </div>
          )}
        </div>
      )}

      <div className="sleep-mode-config">
        <h4>Configuration</h4>
        
        <div className="config-section">
          <div className="config-group">
            <label>Autonomy Level:</label>
            <select
              value={config?.autonomyLevel || 'balanced'}
              onChange={(e) => handleConfigChange('autonomyLevel', e.target.value)}
              disabled={status?.isActive}
              className="config-select"
            >
              <option value="minimal">Conservative - Ask before every action</option>
              <option value="balanced">Balanced - Ask for significant decisions</option>
              <option value="high">Aggressive - Only ask for critical decisions</option>
            </select>
          </div>

          <div className="config-group">
            <label>Max Changes Per Hour:</label>
            <input
              type="number"
              value={config.maxChangesPerHour}
              onChange={(e) => handleConfigChange('maxChangesPerHour', parseInt(e.target.value))}
              disabled={status.isActive}
              min="1"
              max="100"
              className="config-input"
            />
          </div>

          <div className="config-checkboxes">
            <div className="checkbox-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={config.autoCommit}
                  onChange={(e) => handleConfigChange('autoCommit', e.target.checked)}
                  disabled={status.isActive}
                />
                <span className="checkbox-text">Auto-commit approved changes</span>
              </label>
            </div>

            <div className="checkbox-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={config.requiresHumanReview}
                  onChange={(e) => handleConfigChange('requiresHumanReview', e.target.checked)}
                  disabled={status.isActive}
                />
                <span className="checkbox-text">Require human review for critical changes</span>
              </label>
            </div>
          </div>
        </div>

        <div className="thresholds-section">
          <h5>Quality Thresholds</h5>
          
          <div className="thresholds-grid">
            <div className="threshold-item">
              <label>Code Quality: {config.thresholds.codeQuality}%</label>
              <input
                type="range"
                min="50"
                max="100"
                value={config.thresholds.codeQuality}
                onChange={(e) => handleThresholdChange('codeQuality', parseInt(e.target.value))}
                disabled={status.isActive}
                className="threshold-slider quality"
              />
            </div>

            <div className="threshold-item">
              <label>Security Risk: {config.thresholds.securityRisk}%</label>
              <input
                type="range"
                min="0"
                max="50"
                value={config.thresholds.securityRisk}
                onChange={(e) => handleThresholdChange('securityRisk', parseInt(e.target.value))}
                disabled={status.isActive}
                className="threshold-slider security"
              />
            </div>

            <div className="threshold-item">
              <label>Performance Impact: {config.thresholds.performanceImpact}%</label>
              <input
                type="range"
                min="0"
                max="50"
                value={config.thresholds.performanceImpact}
                onChange={(e) => handleThresholdChange('performanceImpact', parseInt(e.target.value))}
                disabled={status.isActive}
                className="threshold-slider performance"
              />
            </div>

            <div className="threshold-item">
              <label>Test Coverage: {config.thresholds.testCoverage}%</label>
              <input
                type="range"
                min="50"
                max="100"
                value={config.thresholds.testCoverage}
                onChange={(e) => handleThresholdChange('testCoverage', parseInt(e.target.value))}
                disabled={status.isActive}
                className="threshold-slider coverage"
              />
            </div>
          </div>
        </div>
      </div>

      <div className="sleep-mode-actions">
        {!status?.isActive ? (
          <button
            className="enable-sleep-mode-btn"
            onClick={() => onToggleSleepMode && onToggleSleepMode(true)}
          >
            🌙 Enable Sleep Mode
          </button>
        ) : (
          <button
            className="disable-sleep-mode-btn"
            onClick={() => onToggleSleepMode && onToggleSleepMode(false)}
          >
            ☀️ Disable Sleep Mode
          </button>
        )}
      </div>

      {!supervisionEnabled && !status?.isActive && (
        <div className="supervision-notice">
          <p>💡 Sleep Mode requires supervision to be enabled for autonomous decision making.</p>
        </div>
      )}
    </div>
  );
};

export default SleepModeManager;