import React, { useState, useRef, useEffect } from 'react';
import './MenuDropdown.css';

const MenuDropdown = ({ 
  trigger, 
  isOpen, 
  onClose, 
  items = [], 
  position = 'left' 
}) => {
  const dropdownRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        onClose();
      }
    };

    const handleKeyDown = (event) => {
      if (event.key === 'Escape') {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
      document.addEventListener('keydown', handleKeyDown);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [isOpen, onClose]);

  const handleItemClick = (item) => {
    if (item.action && typeof item.action === 'function') {
      item.action();
    }
    onClose();
  };

  const renderItem = (item, index) => {
    if (item.type === 'separator') {
      return <div key={index} className="menu-separator" />;
    }

    if (item.type === 'submenu') {
      return (
        <div key={index} className="menu-item submenu-item">
          <div className="menu-item-content">
            <span className="menu-item-icon">{item.icon}</span>
            <span className="menu-item-label">{item.label}</span>
            <span className="menu-item-arrow">▶</span>
          </div>
          {item.shortcut && (
            <span className="menu-item-shortcut">{item.shortcut}</span>
          )}
          <div className="submenu-dropdown">
            <div className="menu-dropdown-content">
              {item.items && item.items.map((subItem, subIndex) => (
                <button
                  key={subIndex}
                  className={`menu-item ${subItem.disabled ? 'disabled' : ''}`}
                  onClick={() => handleItemClick(subItem)}
                  disabled={subItem.disabled}
                >
                  <span className="menu-item-content">
                    {subItem.icon && <span className="menu-item-icon">{subItem.icon}</span>}
                    <span className="menu-item-label">{subItem.label}</span>
                  </span>
                  {subItem.shortcut && (
                    <span className="menu-item-shortcut">{subItem.shortcut}</span>
                  )}
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    }

    return (
      <button
        key={index}
        className={`menu-item ${item.disabled ? 'disabled' : ''}`}
        onClick={() => handleItemClick(item)}
        disabled={item.disabled}
      >
        <span className="menu-item-content">
          {item.icon && <span className="menu-item-icon">{item.icon}</span>}
          <span className="menu-item-label">{item.label}</span>
        </span>
        {item.shortcut && (
          <span className="menu-item-shortcut">{item.shortcut}</span>
        )}
      </button>
    );
  };

  if (!isOpen) return null;

  return (
    <div 
      ref={dropdownRef}
      className={`menu-dropdown ${position}`}
      role="menu"
      aria-orientation="vertical"
    >
      <div className="menu-dropdown-content">
        {items.map((item, index) => renderItem(item, index))}
      </div>
    </div>
  );
};

export default MenuDropdown;