import React, { useState, useEffect } from 'react';
import './SleepModeControls.css';

const SleepModeControls = ({ status, onToggleSleepMode, onPauseSleepMode, onResumeSleepMode, config, onConfigChange }) => {
  const [showConfig, setShowConfig] = useState(false);

  const getRuntime = () => {
    if (!status?.startTime) return '0h 0m';
    const now = new Date();
    const diff = now - status.startTime;
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    return `${hours}h ${minutes}m`;
  };

  const getEfficiencyRate = () => {
    if (!status) return 0;
    const total = status.changesApproved + status.changesRejected;
    if (total === 0) return 100;
    return Math.round((status.changesApproved / total) * 100);
  };

  return (
    <div className="sleep-mode-controls">
      <div className="sleep-mode-header">
        <div className="header-content">
          <h3>🌙 Sleep Mode Controls</h3>
          <p>Quick controls for autonomous development</p>
        </div>
        <div className="sleep-mode-status">
          {status?.isActive ? (
            <div className="status-indicator active">
              🟢 Active
            </div>
          ) : (
            <div className="status-indicator inactive">
              ⚫ Inactive
            </div>
          )}
        </div>
      </div>

      {status?.isActive && (
        <div className="sleep-mode-stats">
          <div className="stats-compact">
            <div className="stat-item">
              <span className="stat-label">Runtime</span>
              <span className="stat-value">{getRuntime()}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Approved</span>
              <span className="stat-value approved">{status.changesApproved}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Rejected</span>
              <span className="stat-value rejected">{status.changesRejected}</span>
            </div>
            <div className="stat-item">
              <span className="stat-label">Efficiency</span>
              <span className="stat-value efficiency">{getEfficiencyRate()}%</span>
            </div>
          </div>
        </div>
      )}

      <div className="sleep-mode-actions">
        {!status?.isActive ? (
          <button 
            className="btn btn-primary enable-btn"
            onClick={() => onToggleSleepMode(true)}
          >
            🌙 Enable Sleep Mode
          </button>
        ) : (
          <div className="active-controls">
            <button 
              className="btn btn-warning pause-btn"
              onClick={onPauseSleepMode}
              title="Pause sleep mode temporarily"
            >
              ⏸️ Pause
            </button>
            <button 
              className="btn btn-danger stop-btn"
              onClick={() => onToggleSleepMode(false)}
              title="Stop sleep mode completely"
            >
              🛑 Stop
            </button>
          </div>
        )}
        
        <button 
          className={`btn btn-secondary config-btn ${showConfig ? 'active' : ''}`}
          onClick={() => setShowConfig(!showConfig)}
          title="Configure sleep mode settings"
        >
          ⚙️ {showConfig ? 'Hide Config' : 'Configure'}
        </button>
      </div>

      {showConfig && (
        <div className="sleep-mode-config">
          <h4>Quick Configuration</h4>
          
          <div className="config-grid">
            <div className="config-group">
              <label>Autonomy Level:</label>
              <select 
                value={config?.autonomyLevel || 'balanced'}
                onChange={(e) => onConfigChange('autonomyLevel', e.target.value)}
                disabled={status?.isActive}
                className="config-select"
              >
                <option value="minimal">Conservative</option>
                <option value="balanced">Balanced</option>
                <option value="high">Aggressive</option>
              </select>
            </div>

            <div className="config-group">
              <label>Max Changes/Hour:</label>
              <input
                type="number"
                value={config?.maxChangesPerHour || 10}
                onChange={(e) => onConfigChange('maxChangesPerHour', parseInt(e.target.value))}
                disabled={status?.isActive}
                min="1"
                max="100"
                className="config-input"
              />
            </div>

            <div className="config-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={config?.autoCommit || false}
                  onChange={(e) => onConfigChange('autoCommit', e.target.checked)}
                  disabled={status?.isActive}
                />
                <span>Auto-commit changes</span>
              </label>
            </div>

            <div className="config-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={config?.requiresHumanReview || false}
                  onChange={(e) => onConfigChange('requiresHumanReview', e.target.checked)}
                  disabled={status?.isActive}
                />
                <span>Require human review</span>
              </label>
            </div>
          </div>

          <div className="config-thresholds">
            <h5>Quality Thresholds</h5>
            <div className="threshold-group">
              <label>Code Quality: {config?.thresholds?.codeQuality || 80}%</label>
              <input
                type="range"
                min="50"
                max="100"
                value={config?.thresholds?.codeQuality || 80}
                onChange={(e) => onConfigChange('threshold', { key: 'codeQuality', value: parseInt(e.target.value) })}
                disabled={status?.isActive}
                className="threshold-slider"
              />
            </div>
            <div className="threshold-group">
              <label>Security Risk: {config?.thresholds?.securityRisk || 30}%</label>
              <input
                type="range"
                min="0"
                max="50"
                value={config?.thresholds?.securityRisk || 30}
                onChange={(e) => onConfigChange('threshold', { key: 'securityRisk', value: parseInt(e.target.value) })}
                disabled={status?.isActive}
                className="threshold-slider"
              />
            </div>
          </div>
        </div>
      )}

      {status?.isActive && (
        <div className="sleep-mode-notice">
          <p>🤖 Sleep mode is running autonomously. Changes are being supervised and approved automatically based on your configured thresholds.</p>
        </div>
      )}
    </div>
  );
};

export default SleepModeControls;