import React, { useState } from 'react';
import './SectionSelector.css';

const SectionSelector = ({ 
  selectedSection, 
  onClose, 
  onReplace, 
  suggestions = [] 
}) => {
  const [selectedCommand, setSelectedCommand] = useState('');
  const [customCommand, setCustomCommand] = useState('');
  const [isCustom, setIsCustom] = useState(false);

  if (!selectedSection) return null;

  const handleReplace = () => {
    const command = isCustom ? customCommand : selectedCommand;
    if (command.trim()) {
      onReplace(selectedSection, command);
      onClose();
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setSelectedCommand(suggestion);
    setIsCustom(false);
  };

  const handleCustomInput = (value) => {
    setCustomCommand(value);
    setIsCustom(true);
    setSelectedCommand('');
  };

  return (
    <div className="section-selector-overlay">
      <div className="section-selector-modal">
        <div className="modal-header">
          <h3>🎨 Replace {selectedSection.name}</h3>
          <button className="close-btn" onClick={onClose}>✕</button>
        </div>
        
        <div className="modal-content">
          <div className="section-info">
            <div className="section-type-badge">
              {selectedSection.type.toUpperCase()}
            </div>
            <p>Select a React Bits command to replace this section with an AI-generated component.</p>
          </div>

          <div className="suggestions-section">
            <h4>💡 Suggested Commands</h4>
            <div className="suggestions-grid">
              {suggestions.map((suggestion, index) => (
                <button
                  key={index}
                  className={`suggestion-btn ${selectedCommand === suggestion ? 'selected' : ''}`}
                  onClick={() => handleSuggestionClick(suggestion)}
                >
                  <code>{suggestion}</code>
                </button>
              ))}
            </div>
          </div>

          <div className="custom-section">
            <h4>✏️ Custom Command</h4>
            <div className="custom-input-wrapper">
              <input
                type="text"
                className="custom-input"
                placeholder="/ui create your own component..."
                value={customCommand}
                onChange={(e) => handleCustomInput(e.target.value)}
              />
              {isCustom && customCommand && (
                <div className="command-preview">
                  <span>Preview: </span>
                  <code>{customCommand}</code>
                </div>
              )}
            </div>
          </div>

          <div className="command-examples">
            <h5>💡 Command Examples</h5>
            <ul>
              <li><code>/ui create a {selectedSection.type} with purple theme</code></li>
              <li><code>/ui build a modern {selectedSection.type} with animations</code></li>
              <li><code>/ui make a responsive {selectedSection.type} with gradients</code></li>
            </ul>
          </div>
        </div>

        <div className="modal-footer">
          <button className="cancel-btn" onClick={onClose}>
            Cancel
          </button>
          <button 
            className="replace-btn" 
            onClick={handleReplace}
            disabled={!selectedCommand && !customCommand.trim()}
          >
            ✨ Generate with React Bits
          </button>
        </div>
      </div>
    </div>
  );
};

export default SectionSelector;